import * as actions from '../../../actions';
import * as keyCodes from '../../../constants/keyCodes';
import {
	NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS
} from '../../../constants/cssClasses';
import * as labels from '../../../constants/debugPointLabel';

/**
 *
 * @param {Number} lineNumber lineNumber where to add/update debugpoint
 * @param {{}} breakPoint breakPoint object at the linenumber
 * @param {{}} logPoint logpoint object at the linenumber
 *
 * Returns items for the contextmenu to shown at the particular line
 */
function getGutterContextMenuItems(
	lineNumber,
	breakPoint,
	logPoint,
	logPointRole,
	scriptDebuggerRole,
	logPointSystemPreference
) {
	const menuItems = getItems(lineNumber);
	let contextMenuItems = [];

	if (breakPoint) {
		if (scriptDebuggerRole) {
			if (breakPoint.fEvaluationString === '') {
				contextMenuItems = [menuItems.rem_bp, menuItems.add_con];
			} else {
				contextMenuItems = [menuItems.rem_bp, menuItems.edit_con, menuItems.rem_con];
			}
		}
	} else if (logPoint) {
		if (logPointRole && logPointSystemPreference) {
			contextMenuItems = [menuItems.edit_lp, menuItems.rem_lp];
		}
	} else {
		if (scriptDebuggerRole) {
			contextMenuItems.push(menuItems.add_bp, menuItems.add_cbp);
		}
		if (logPointRole && logPointSystemPreference) {
			contextMenuItems.push(menuItems.add_lp);
		}
	}

	return contextMenuItems;
}

/**
 *
 * @param {Number} lineNumber lineNumber where to add/update debugpoint to be passes as options
 *
 * Returns all the items which are available for the contextmenu
 */
function getItems(lineNumber) {
	const contextMenuItems = {
		rem_bp: {
			id: 'rem_bp',
			label: labels.REMOVE_BREAKPOINT,
			options: { lineNumber, action: actions.REMOVE_BREAKPOINT }
		},
		add_con: {
			id: 'add_con',
			label: labels.ADD_CONDITION,
			options: { lineNumber, action: actions.ADD_CONDITION }
		},
		edit_con: {
			id: 'edit_con',
			label: labels.EDIT_CONDITION,
			options: { lineNumber, action: actions.EDIT_CONDITION }
		},
		rem_con: {
			id: 'rem_con',
			label: labels.REMOVE_CONDITION,
			options: { lineNumber, action: actions.REMOVE_CONDITION }
		},
		rem_lp: {
			id: 'rem_lp',
			label: labels.REMOVE_LOGPOINT,
			options: { lineNumber, action: actions.REMOVE_LOGPOINT }
		},
		edit_lp: {
			id: 'edit_lp',
			label: labels.EDIT_LOGPOINT,
			options: { lineNumber, action: actions.EDIT_LOGPOINT }
		},
		add_bp: {
			id: 'add_bp',
			label: labels.ADD_BREAKPOINT,
			options: { lineNumber, action: actions.ADD_BREAKPOINT }
		},
		add_lp: {
			id: 'add_lp',
			label: labels.ADD_LOGPOINT,
			options: { lineNumber, action: actions.ADD_LOGPOINT }
		},
		add_cbp: {
			id: 'add_cbp',
			label: labels.ADD_CONDITIONAL_BREAKPOINT,
			options: { lineNumber, action: actions.ADD_CONDITIONAL_BREAKPOINT }
		}
	};
	return contextMenuItems;
}

/**
 *
 * @param {{}} coeffects Seismic event object
 *
 * Performs action based on the type of action from the payload to add/remove/update debugpoint
 */
function gutterContextMenuHandle({
	action: {
		payload: {
			item: {
				options: { lineNumber, action }
			}
		}
	},
	state: { editor, breakPoints, logPoints },
	properties: { sysId, recordType, scriptField },
	dispatch
}) {
	if (action === actions.REMOVE_BREAKPOINT || action === actions.REMOVE_LOGPOINT) {
		dispatch(action, { sysId, recordType, scriptField, lineNumber });
	} else if (action === actions.ADD_BREAKPOINT || action === actions.REMOVE_CONDITION) {
		const data = { evaluationString: '' };
		dispatch(action, { sysId, recordType, scriptField, lineNumber, data });
	} else {
		const debugPoint = breakPoints[lineNumber] || logPoints[lineNumber];
		// we are closing any previously opened widget nd we will revert the decorations for line for previously opened widget
		const debugService = editor.debugPointService();
		if (debugService.viewZoneId !== null) {
			debugService.removeInputWidget();
			const lineNumber = debugService.decoratedLineNumber;
			revertDeltaDecorationsAtLine(lineNumber, debugService, breakPoints, logPoints);
		}
		debugService.addInputWidget(
			lineNumber,
			debugPoint ? debugPoint.fEvaluationString : '',
			action === actions.ADD_LOGPOINT || action === actions.EDIT_LOGPOINT
				? labels.LOGPOINT_ACTION
				: labels.BREAKPOINT_ACTION
		);
		const inputWidgetNode = debugService.domNode;

		//Due to an Monaco bug we have to do the timeout, otherwise we do not get focus
		setTimeout(() => inputWidgetNode.focus(), 150);

		inputWidgetNode.onkeydown = e => {
			if (e.key === keyCodes.Key_Enter) {
				handleInputWidget(debugService, action, dispatch, sysId, recordType, scriptField, lineNumber);
			} else if (e.key === keyCodes.Key_Esc) {
				debugService.removeInputWidget();
				revertDeltaDecorationsAtLine(lineNumber, debugService, breakPoints, logPoints);
			}
		};
	}

	function revertDeltaDecorationsAtLine(lineNumber, debugService, breakPoints, logPoints) {
		const debugPoint = breakPoints[lineNumber] || logPoints[lineNumber];
		debugPoint
			? debugService.updateDebugPointDecoration(
					lineNumber,
					debugPoint.fEvaluationString,
					breakPoints[lineNumber]
						? debugPoint.fEvaluationString === ''
							? NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS
							: NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS
						: NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS
			  )
			: debugService.removeDebugPointDecoration(lineNumber);
		debugService.decoratedLineNumber = null;
	}

	function handleInputWidget(debugService, action, dispatch, sysId, recordType, scriptField, lineNumber) {
		debugService.removeInputWidget();

		const data = { evaluationString: debugService.domNode.value };

		if (action === actions.ADD_LOGPOINT && data.evaluationString === '') {
			debugService.removeDebugPointDecoration(lineNumber);
		} else if (action === actions.EDIT_LOGPOINT && data.evaluationString === '') {
			dispatch(action, { sysId, recordType, scriptField, lineNumber });
		} else {
			dispatch(action, { sysId, recordType, scriptField, lineNumber, data });
		}
	}
}

export { getGutterContextMenuItems, gutterContextMenuHandle };
