import * as actions from '../../actions';
import { breakPointEffect, logPointEffect } from './helpers/debugPointHttpEffect';
import handleGutterClick from './helpers/handleGutterClick';
import { updateBreakPoint, updateLogPoint } from './helpers/updateDebugPoints';
import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';

const debugPointHandlers = {
	[actions.ADD_BREAKPOINT]: breakPointEffect,
	[actions.ADD_CONDITIONAL_BREAKPOINT]: breakPointEffect,
	[actions.ADD_LOGPOINT]: logPointEffect,
	[actions.EDIT_LOGPOINT]: logPointEffect,
	[actions.REMOVE_LOGPOINT]: logPointEffect,
	[actions.REMOVE_BREAKPOINT]: breakPointEffect,
	[actions.ADD_CONDITION]: breakPointEffect,
	[actions.REMOVE_CONDITION]: breakPointEffect,
	[actions.EDIT_CONDITION]: breakPointEffect,
	[actions.GUTTER_CLICKED]: {
		effect(coeffects) {
			handleGutterClick(coeffects);
		},
		stopPropagation: true
	},
	[actions.TOGGLE_BREAKPOINT_SUCCEEDED]: {
		effect(coeffects) {
			updateBreakPoint(coeffects);
		},
		stopPropagation: true
	},
	[actions.TOGGLE_LOGPOINT_SUCCEEDED]: {
		effect(coeffects) {
			updateLogPoint(coeffects);
		},
		stopPropagation: true
	},
	[actions.HTTP_EFFECT_RESULTS_ERROR]: {
		effect(coeffects) {
			//TODO
		},
		stopPropagation: true
	},
	[actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST]: {
		...createAmbSubscriptionEffect('/rw/default/:table/:filter', {
			subscribeStartedActionType: actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST_STARTED,
			messageReceivedActionType: actions.DEBUGPOINTS_UPDATED,
			subscribeSucceededActionType: actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST_SUCCEEDED,
			subscribeFailedActionType: actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST_FAILED
		}),
		stopPropagation: true
	},
	[actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST_STARTED]: {
		effect(coeffects) {
			//if we want to perform any action on breakpoints AMB started, we can add it here
		},
		stopPropagation: true
	},
	[actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST_SUCCEEDED]: {
		effect({ updateState }) {
			updateState({ ambSubscribed: true });
		},
		stopPropagation: true
	},
	[actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST_FAILED]: {
		effect(coeffects) {
			//if we want to perform any action on AMB reuqest failed, we can add it here
		},
		stopPropagation: true
	},
	[actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST]: {
		...createAmbSubscriptionEffect('/rw/default/:table/:filter', {
			subscribeStartedActionType: actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST_STARTED,
			messageReceivedActionType: actions.DEBUGPOINTS_UPDATED,
			subscribeSucceededActionType: actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST_SUCCEEDED,
			subscribeFailedActionType: actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST_FAILED
		}),
		stopPropagation: true
	},
	[actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST_STARTED]: {
		effect(coeffects) {
			//if we want to perform any action on logpoints AMB started, we can add it here
		},
		stopPropagation: true
	},
	[actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST_SUCCEEDED]: {
		effect({ updateState }) {
			updateState({ ambSubscribed: true });
		},
		stopPropagation: true
	},
	[actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST_FAILED]: {
		effect(coeffects) {
			//if we want to perform any action on AMB reuqest failed, we can add it here
		},
		stopPropagation: true
	},
	[actions.DEBUGPOINTS_UPDATED]: {
		effect({ state: { ambSubscribed }, dispatch, properties: { recordType, scriptField, sysId } }) {
			if (ambSubscribed === true) {
				dispatch(actions.DEBUG_POINTS_DATA_REQUEST_AMB, { recordType, scriptField, sysId });
			}
		},
		stopPropagation: true
	}
};

export default debugPointHandlers;
