import { getActiveElement } from '@servicenow/library-enhanced-utils';
import * as accessibilityConstant from '../now-code-editor/constants/accessibility';

const moveControl = moveDirection => {
	try {
		const element = getActiveElement();
		const currentIndex = parseInt(element.getAttribute('id'));
		if (isNaN(currentIndex)) {
			return;
		}
		const length = element.parentNode.parentNode.children.length;
		let nextIndex = 0;
		if (moveDirection === accessibilityConstant.ARROW_DOWN) {
			nextIndex = currentIndex + 1;
		}
		if (moveDirection === accessibilityConstant.ARROW_UP) {
			nextIndex = currentIndex - 1;
		}
		if (nextIndex === length || nextIndex === -1) {
			return;
		}
		const elementToFocus = element.parentNode.parentNode.children[nextIndex].children[1];

		element.setAttribute('tabIndex', '-1');
		elementToFocus.setAttribute('tabIndex', '0');
		elementToFocus.focus();
	} catch (e) {}
};
const eventHandlers = [
	{
		events: ['keydown'],
		effect: ({
			action: {
				payload: { event: event }
			}
		}) => {
			if (event.key === accessibilityConstant.ARROW_DOWN) {
				event.preventDefault();
				moveControl(accessibilityConstant.ARROW_DOWN);
			}
			if (event.key === accessibilityConstant.ARROW_UP) {
				event.preventDefault();
				moveControl(accessibilityConstant.ARROW_UP);
			}
			if (event.key === accessibilityConstant.ARROW_RIGHT) {
				event.preventDefault();
			}
			if (event.key === accessibilityConstant.ARROW_LEFT) {
				event.preventDefault();
			}
		}
	}
];

export default eventHandlers;
