import '@servicenow/now-toggle';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { CLOSED_MORE_OPTIONS, DROP_DOWN_MENU_ITEM_CLICKED } from '../now-code-editor/actions';
import * as dropDownSections from '../now-code-editor/constants/toolbar/dropDownSections';
import * as dropDownItemTypes from '../now-code-editor/constants/toolbar/dropDownItemTypes';

const renderToggleItem = (item, dispatch) => {
	return (
		<div
			className="sn-code-editor-toolbar-dropdown-item"
			on-click={() => {
				if (!item.disabled) {
					dispatch(DROP_DOWN_MENU_ITEM_CLICKED, { action: item.action });
				}
			}}
		>
			<span className="sn-code-editor-toolbar-dropdown-item-label">{item.label}</span>
			<now-toggle
				manageChecked
				disabled={item.disabled}
				checked={item.selected}
				className="sn-code-editor-toolbar-dropdown-item-icon"
				size="sm"
				config-aria={{
					switch: { 'aria-label': item.label }
				}}
			/>
		</div>
	);
};

const renderExternal = (item, dispatch) => {
	return (
		<div
			className="sn-code-editor-toolbar-dropdown-item external-item"
			// eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex
			tabIndex={0}
			on-click={() => {
				dispatch(CLOSED_MORE_OPTIONS);
				setTimeout(() => dispatch(item.action), 100);
			}}
			role="button"
		>
			<span className="sn-code-editor-toolbar-dropdown-item-label">{item.label}</span>
		</div>
	);
};

const renderSection = (section, dispatch) => {
	if (section.items.length == 0) {
		return '';
	}
	return (
		<Fragment>
			<div className="sn-code-editor-toolbar-dropdown-section">
				<div className="sn-code-editor-toolbar-dropdown-section-label">{section.label}</div>
			</div>
			{section.items.map(item => {
				switch (item.type) {
					case dropDownItemTypes.TOGGLE:
						return renderToggleItem(item, dispatch);
					case dropDownItemTypes.EXTERNAL:
						return renderExternal(item, dispatch);
					default:
						break;
				}
			})}
		</Fragment>
	);
};

const view = (state, { dispatch }) => {
	const { dropDownItems } = state.properties;
	const editCodeSection = dropDownItems[dropDownSections.EDIT_CODE];
	const viewSection = dropDownItems[dropDownSections.VIEW];
	const settingSection = dropDownItems[dropDownSections.SETTINGS];
	const getHelpSection = dropDownItems[dropDownSections.GET_HELP];

	return (
		<div className="sn-code-editor-toolbar-dropdown">
			{renderSection(editCodeSection, dispatch)}
			{renderSection(viewSection, dispatch)}
			{renderSection(settingSection, dispatch)}
			{renderSection(getHelpSection, dispatch)}
		</div>
	);
};

export default view;
