import { JAVASCRIPT, HTML, JSON, XML, CSS } from './constants/supportedLanguages';
import { BLACK, DARK, LIGHT } from './constants/availableThemes';
import { APPEND, REPLACE } from './constants/autocomplete';
import { INFO, HINT, WARNING, ERROR } from './constants/messageSeverities';

const properties = {
	content: {},
	cursorPosition: {
		schema: {
			type: 'object',
			properties: {
				lineNumber: { type: 'number' },
				column: { type: 'number' }
			},
			required: ['lineNumber', 'column']
		},
		default: { lineNumber: 1, column: 1 }
	},
	language: {
		default: JAVASCRIPT,
		schema: {
			type: 'string',
			enum: [JAVASCRIPT, XML, JSON, HTML, CSS]
		}
	},
	readOnly: {
		default: false,
		schema: { type: 'boolean' }
	},
	theme: {
		default: LIGHT,
		schema: { type: 'string', enum: [LIGHT, DARK, BLACK] }
	},
	hideToolbar: {
		default: false,
		schema: { type: 'boolean' }
	},
	hideGutter: {
		default: false,
		schema: { type: 'boolean' }
	},
	disableSyntaxHighlighting: {
		default: false,
		schema: { type: 'boolean' }
	},
	//javascript specific properties
	scope: {
		default: 'global'
	},
	recordType: {},
	scriptField: {},
	sysId: {},

	disableDebugging: {
		default: false,
		schema: { type: 'boolean' }
	},
	linting: {
		schema: {
			type: 'object',
			properties: {
				disabled: { type: 'boolean' },
				linterUrl: { type: 'string' },
				lintConfig: { type: 'object' }
			}
		},
		default: {
			disabled: false
		}
	},
	disableContextmenu: {
		//TODO: disableContextmenu default should be false, once we have discoverability feature
		default: true,
		schema: { type: 'boolean' }
	},
	disableFullScreen: {
		default: false,
		schema: { type: 'boolean' }
	},
	disableMinimap: {
		default: false,
		schema: { type: 'boolean' }
	},

	//Temporary property for type (client or server)
	isClientScript: {
		default: false,
		schema: { type: 'boolean' }
	},
	width: {
		default: '100%',
		schema: { type: 'string' }
	},
	height: {
		default: '100%',
		schema: { type: 'string' }
	},
	autocompleteSuggestions: {
		schema: {
			type: 'object',
			properties: {
				disabled: { type: 'boolean' },
				variant: { type: 'string', enum: [APPEND, REPLACE] },
				suggestions: { type: 'array', items: { type: 'string' } }
			}
		},
		default: { disabled: false }
	},
	markers: {
		schema: {
			type: 'array',
			items: {
				type: 'object',
				properties: {
					message: { type: 'string' },
					severity: { type: 'string', enum: [INFO, HINT, WARNING, ERROR] },
					from: {
						type: 'object',
						properties: { line: { type: 'number' }, column: { type: 'number' } },
						required: ['line', 'column']
					},
					to: {
						type: 'object',
						properties: { line: { type: 'number' }, column: { type: 'number' } },
						required: ['line', 'column']
					}
				},
				required: ['message', 'severity', 'from', 'to']
			}
		},
		default: []
	},
	customCSS: {
		schema: { type: 'string' }
	}
};

export default properties;
