import { t } from 'sn-translate';
import { SET_MACROS } from '../../../actions';

function macrosHandler({
	action: {
		payload: { data, errors }
	},
	dispatch
}) {
	if (errors.length === 0 && data && data.GlideNowCodeEditor_Query) {
		const macrosResponse = data.GlideNowCodeEditor_Query;
		if (macrosResponse.macrosMetaData && macrosResponse.macrosMetaData.macros) {
			const macrosList = macrosResponse.macrosMetaData.macros;
			if (Array.isArray(macrosList) && macrosList.length > 0) {
				//Removing macros which do not have proper tag or text
				let macros = macrosList.filter(({ name = '', text = '' }) => {
					return name && text;
				});
				macros = addHelpMacro(macros);
				dispatch(SET_MACROS, { macros });
			}
		}
	}
}

//Appending help macro - this macro describes all macros
function addHelpMacro(macrosList) {
	let helpMacro = t('The Syntax Editor macros are:\n-----------------------------');
	macrosList.forEach(({ name, comments }) => {
		helpMacro = `${helpMacro}\n${name} - ${comments || ''}`;
	});
	macrosList.push({
		name: 'help',
		comments: t('Editor macros help'),
		text: helpMacro
	});
	return macrosList;
}

export default macrosHandler;
