import { JAVASCRIPT } from '../constants/supportedLanguages';
import initializeEditorCacheWorker from './editorFeatures/editorCacheWorkerUtils';
import { initializeDiscoverWorker, discoverEditorTokens, getDiscoveredLiteral } from './editorFeatures/discoverability';
import { initLintWorker, lintValidator } from './editorFeatures/linting';

export default class AbstractEditor {
	element = null;
	eslintConfig = null;
	enableSyntaxCheck = null;
	instance = null;
	_options = {};
	onContentChangedCallback = null;
	_apisDocResourceIds = {};
	lintWorker = {
		onmessage: fn => {
			if (this.lintWorker._workerInstance) {
				this.lintWorker._workerInstance.onmessage = data => {
					fn(data);
				};
			}
		},
		postMessage: data => {
			if (this.lintWorker._workerInstance) {
				this.lintWorker._workerInstance.postMessage(data);
			}
		},
		_workerInstance: null
	};
	_discoverWorker = null;
	_editorCacheWorker = null;
	_autoCompleteWorker = null;
	isFullScreen = false;
	_isAutocompleteEnabled = false;
	debugPointInstance = null;

	constructor(element = null, options = {}) {
		if (new.target === AbstractEditor) {
			throw Error('Head back and initalize your favourite editor');
		}
		this.element = element;
		this._options = options;
		return this;
	}

	set options(options) {
		this._options = options;
	}

	get options() {
		return this._options;
	}

	get discoverWorker() {
		return this._discoverWorker;
	}

	set discoverWorker(discoverWorker) {
		this._discoverWorker = discoverWorker;
	}

	get editorCacheWorker() {
		return this._editorCacheWorker;
	}

	set editorCacheWorker(editorCacheWorker) {
		this._editorCacheWorker = editorCacheWorker;
	}

	get autoCompleteWorker() {
		return this._autoCompleteWorker;
	}

	set autoCompleteWorker(autoCompleteWorker) {
		this._autoCompleteWorker = autoCompleteWorker;
	}

	get apisDocResourceIds() {
		return this._apisDocResourceIds;
	}

	set apisDocResourceIds(apisDocResourceIds) {
		this._apisDocResourceIds = apisDocResourceIds;
	}

	get isAutocompleteEnabled() {
		return this._isAutocompleteEnabled;
	}

	set isAutocompleteEnabled(isAutocompleteEnabled) {
		this._isAutocompleteEnabled = isAutocompleteEnabled;
	}

	initLint(eslintConfig, linterUrl) {
		const lintWorkerCreated = initLintWorker.call(this, linterUrl);

		if (lintWorkerCreated) {
			this.eslintConfig = eslintConfig || {};
			this.enableSyntaxCheck = true;
			lintValidator.call(this);
		}
	}

	disableLint() {
		this.removeLintErrors();
		this.enableSyntaxCheck = false;
	}

	toggleSyntaxCheck() {
		if (this.enableSyntaxCheck) {
			this.removeLintErrors();
		} else {
			lintValidator.call(this);
		}
		this.enableSyntaxCheck = !this.enableSyntaxCheck;
	}

	updateOptions(key, value) {
		this.options[key] = value;
	}

	intialiseEditorCache(apisDocResourceFlushTime) {
		return new Promise((resolve, reject) => {
			if (!this.editorCacheWorker) {
				this.editorCacheWorker = initializeEditorCacheWorker();
				if (this.editorCacheWorker) {
					this.editorCacheWorker.loadCache(apisDocResourceFlushTime, data => {
						resolve({ editorCacheReloaded: data.cacheReloaded });
					});
				} else {
					reject('Editor cache worker failed to initalize');
				}
			} else {
				resolve({ editorCacheReloaded: false });
			}
		});
	}

	enableDiscoverability(apisDocResourceFlushTime, apisDocResourceIds) {
		try {
			this.apisDocResourceIds = apisDocResourceIds;
			if (!this.discoverWorker) {
				this.intialiseEditorCache(apisDocResourceFlushTime)
					.then(({ editorCache: { editorCacheReloaded } }) => {
						initializeDiscoverWorker.call(this, editorCacheReloaded);
					})
					.catch(() => {});
			} else {
				this.discoverTokens();
			}
		} catch (e) {}
	}

	discoverTokens(startLineNumber = 1) {
		if (this.options.language === JAVASCRIPT && !this.options.disableContextmenu && this.discoverWorker) {
			discoverEditorTokens.call(this, startLineNumber);
		}
	}

	getDiscoveredLiteral(target) {
		return getDiscoveredLiteral.call(this, target);
	}

	formatCode() {}

	renderDebugPoints() {}

	enableAutoComplete() {}

	onEditorContentChanged() {}

	dispatchContentChanged() {}

	markLintErrors() {}

	removeLintErrors() {}

	getEditorLintData() {}
}
