import { TOGGLE_DEBUGGING } from '../../../actions';
import { getSystemPreferenceAsBoolean } from '../helpers/utils';

function debugPointsHandler({
	action: {
		payload: { data, errors }
	},
	updateState,
	dispatch
}) {
	if (errors.length === 0 && data && data.GlideDebugpoint_Query) {
		const debugPointsResponse = data.GlideDebugpoint_Query;
		const debugPointsData = debugPointsResponse.debugPoints;
		if (debugPointsData) {
			let logPoints = {};
			let breakPoints = {};
			try {
				if (debugPointsData.logpoints !== '') {
					logPoints = JSON.parse(debugPointsData.logpoints || '{}');
				}
			} catch (error) {}
			try {
				if (debugPointsData.breakPoints !== '') {
					breakPoints = JSON.parse(debugPointsData.breakpoints || '{}');
				}
			} catch (error) {}

			const logPointRole = debugPointsData.logpointRole;
			const scriptDebuggerRole = debugPointsData.scriptDebuggerRole;

			const logPointSystemPreference = getSystemPreferenceAsBoolean(
				data.GlideNowCodeEditor_Query,
				'logPointSystemPreference'
			);

			updateState({ logPoints, breakPoints });
			dispatch(TOGGLE_DEBUGGING, { logPointRole, scriptDebuggerRole, logPointSystemPreference });
		}
	}
}

export default debugPointsHandler;
