import { getUserPreferenceAsBoolean, getSystemPreferenceAsBoolean } from '../helpers/utils';
import { TOGGLE_LINTING } from '../../../actions';

function lintingHandler({
	action: {
		payload: { data, errors }
	},
	updateState,
	dispatch
}) {
	let isLintingEnabled = false;
	let systemEslintConfig = {};
	if (errors.length === 0 && data && data.GlideNowCodeEditor_Query && data.GlideNowCodeEditor_Query.esLintConfig) {
		const lintingUserPreference = getUserPreferenceAsBoolean(data.GlideDomain_Query, 'lintingUserPreference');
		const lintingSystemPreference = getSystemPreferenceAsBoolean(
			data.GlideNowCodeEditor_Query,
			'lintingSystemPreference'
		);

		if (lintingUserPreference === true && lintingSystemPreference === true) {
			isLintingEnabled = true;
		}
		try {
			const results = data.GlideNowCodeEditor_Query.esLintConfig.value;
			if (results) {
				systemEslintConfig = JSON.parse(results);
			}
		} catch (error) {}
		dispatch(TOGGLE_LINTING);
	}
	updateState({ systemEslintConfig, isLintingEnabled });
}

export default lintingHandler;
