import * as actions from '../now-code-editor/actions';

const actionHandlers = {
	[actions.NOW_BUTTON_ICONIC_CLICKED]: {
		effect({ dispatch, action: { payload } }) {
			dispatch(payload.action);
		},
		stopPropagation: true
	},
	[actions.NOW_BUTTON_STATEFUL_SELECTED_SET]: {
		effect({ dispatch, action: { payload } }) {
			dispatch(payload.action);
		},
		stopPropagation: true
	},
	[actions.DROP_DOWN_MENU_ITEM_CLICKED]: {
		effect: ({ dispatch, action: { payload } }) => {
			dispatch(payload.action);
		},
		stopPropagation: true
	}
};

export default actionHandlers;
