import toolbarItemsConfig from '../../constants/toolbar/toolbarItemsConfig';
import { JAVASCRIPT, JSON } from '../../constants/supportedLanguages';

function generateCodeEditingSection(
	readOnly,
	disableLintingComponentPreference,
	isLintingEnabled,
	language,
	syntaxCheckSelected
) {
	const codeEditingSection = [toolbarItemsConfig.findConfig];

	if (language !== JSON) {
		codeEditingSection.push({ ...toolbarItemsConfig.toggleCommentConfig, disabled: readOnly });
	}

	codeEditingSection.push({ ...toolbarItemsConfig.formatCodeConfig, disabled: readOnly });

	if (disableLintingComponentPreference === false && language === JAVASCRIPT) {
		const disabled = isLintingEnabled === false ? true : false;

		codeEditingSection.push({
			...toolbarItemsConfig.toggleSyntaxCheckConfig,
			selected: syntaxCheckSelected,
			disabled
		});
	}
	return codeEditingSection;
}

function generateNavigationActionsSection(
	disableDebuggingComponentPreference,
	disableFullScreenComponentPreference,
	isClientScript,
	language,
	fullScreenSelected
) {
	const navigationActionsSection = [];

	if (!disableFullScreenComponentPreference) {
		if (fullScreenSelected) {
			navigationActionsSection.push({
				...toolbarItemsConfig.fullScreenOnConfig,
				selected: fullScreenSelected
			});
		} else {
			navigationActionsSection.push({
				...toolbarItemsConfig.fullScreenOffConfig,
				selected: fullScreenSelected
			});
		}
	}

	if (disableDebuggingComponentPreference === false && isClientScript === false && language === JAVASCRIPT) {
		navigationActionsSection.push(toolbarItemsConfig.openScriptDebuggerConfig);
	}
	return navigationActionsSection;
}

function generateSections(
	isLintingEnabled,
	readOnly,
	isClientScript,
	disableLintingComponentPreference,
	disableDebuggingComponentPreference,
	disableFullScreenComponentPreference,
	language,
	syntaxCheckSelected,
	fullScreenSelected,
	moreOptionSelected
) {
	const leftAlignedSections = [];
	leftAlignedSections.push(
		generateCodeEditingSection(
			readOnly,
			disableLintingComponentPreference,
			isLintingEnabled,
			language,
			syntaxCheckSelected
		)
	);
	const rightAlignedSections = [];
	const navigationActionsSection = generateNavigationActionsSection(
		disableDebuggingComponentPreference,
		disableFullScreenComponentPreference,
		isClientScript,
		language,
		fullScreenSelected
	);
	if (navigationActionsSection.length !== 0) {
		rightAlignedSections.push(navigationActionsSection);
	}

	rightAlignedSections.push([{ ...toolbarItemsConfig.moreConfig, selected: moreOptionSelected }]);
	const sections = {
		leftAligned: leftAlignedSections,
		rightAligned: rightAlignedSections
	};

	return sections;
}

export default generateSections;
