import {
	NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS
} from '../../../constants/cssClasses';

/**
 *
 * calls method to update breakpoint decorations for
 * every breakpoint at the time of initialization
 */
function renderBreakPoints() {
	const debugService = this.debugPointService();
	const lineCount = this.instance.getModel().getLineCount();

	for (const linenumber in debugService.breakPoints) {
		const lineNumber = parseInt(linenumber);
		if (linenumber > lineCount) {
			return;
		}
		const breakPoint = debugService.breakPoints[linenumber];
		const evaluationString = breakPoint.fEvaluationString;
		if (evaluationString === '') {
			debugService.updateDebugPointDecoration(
				lineNumber,
				evaluationString,
				NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS
			);
		} else {
			debugService.updateDebugPointDecoration(
				lineNumber,
				evaluationString,
				NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS
			);
		}
	}
}

/**
 *
 * calls method to update logpoint decorations for every
 * logpoint at the time of initialization
 */
function renderLogPoints() {
	const debugService = this.debugPointService();
	const lineCount = this.instance.getModel().getLineCount();
	for (const linenumber in debugService.logPoints) {
		const lineNumber = parseInt(linenumber);
		if (linenumber > lineCount) {
			return;
		}
		const logPoint = debugService.logPoints[linenumber];
		const evaluationString = logPoint.fEvaluationString;
		debugService.updateDebugPointDecoration(lineNumber, evaluationString, NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS);
	}
}

export { renderBreakPoints, renderLogPoints };
