import { JAVASCRIPT } from '../../../../constants/supportedLanguages';
import monaco from '../../NowMonaco';
import clientAPI from './clientAPI';
import serverAPI from './serverAPI';
import { REPLACE } from '../../../../constants/autocomplete';

const editorLibraries = {};
let editorLibraryDisposables = [];
let scriptIncludeLibraries = {};
let scriptIncludeDisposables = [];
let focusedEditor = null;

function enableAutoComplete(apisDocResourceFlushTime) {
	const modelId = this.instance.getModel().id;

	if (!editorLibraries[modelId]) {
		registerEditorOnFocus.call(this);
	}

	editorLibraries[modelId] = [];

	if (this.isAutocompleteEnabled) {
		const { isClientScript, autocompleteSuggestions } = this.options;

		if (autocompleteSuggestions.variant !== REPLACE) {
			editorLibraries[modelId].push(isClientScript ? clientAPI : serverAPI);
		}
		const suggestions = autocompleteSuggestions.variant ? autocompleteSuggestions.suggestions || [] : [];
		editorLibraries[modelId].push(...suggestions);

		enableScriptIncludeIntelisense.call(this, apisDocResourceFlushTime);
	}
}

function registerEditorOnFocus() {
	const modelId = this.instance.getModel().id;
	const { language, readOnly } = this.options;

	this.instance.onDidFocusEditorWidget(() => {
		if (focusedEditor !== modelId && language === JAVASCRIPT && !readOnly) {
			focusedEditor = modelId;

			editorLibraryDisposables.forEach(library => {
				library.dispose();
			});
			editorLibraryDisposables = [];

			editorLibraries[modelId].forEach(library => {
				editorLibraryDisposables.push(monaco.languages.typescript.javascriptDefaults.addExtraLib(library));
			});

			if (!this.isAutocompleteEnabled && scriptIncludeDisposables.length > 0) {
				scriptIncludeDisposables.forEach(library => {
					library.dispose();
				});
				scriptIncludeDisposables = [];
			} else if (this.isAutocompleteEnabled && scriptIncludeDisposables.length === 0) {
				for (const scriptIncludeName in scriptIncludeLibraries) {
					scriptIncludeDisposables.push(
						monaco.languages.typescript.javascriptDefaults.addExtraLib(
							scriptIncludeLibraries[scriptIncludeName]
						)
					);
				}
			}
		}
	});
}

function enableScriptIncludeIntelisense(apisDocResourceFlushTime) {
	if (!this.autoCompleteWorker) {
		this.intialiseEditorCache(apisDocResourceFlushTime)
			.then(() => {
				initializeIntellisenseForScriptIncludes(this);
			})
			.catch(() => {});
	}
}

function initializeIntellisenseForScriptIncludes(monacoEditorInstance) {
	initializeAutoCompleteWorker(monacoEditorInstance);
	if (monacoEditorInstance.autoCompleteWorker) {
		connectAutoCompleteWorkerWithEditorCacheWorker(monacoEditorInstance);
		monacoEditorInstance.instance.onDidChangeModelContent(event => {
			const { autocompleteSuggestions, language } = monacoEditorInstance.options;
			if (autocompleteSuggestions.disabled === false && language === JAVASCRIPT) {
				requestScriptIncludeIntellisense(event, monacoEditorInstance);
			}
		});
	}
}

function initializeAutoCompleteWorker(monacoEditorInstance) {
	const autoCompleteWorker = new SharedWorker('../../../../../workers/autoCompleteWorker');
	monacoEditorInstance.autoCompleteWorker = autoCompleteWorker.port;
	monacoEditorInstance.autoCompleteWorker.onmessage = event => {
		onMessage(event, monacoEditorInstance);
	};

	monacoEditorInstance.autoCompleteWorker.postMessage({
		operation: 'requestForExistingTypeDefinitions'
	});
}

function onMessage(event, monacoEditorInstance) {
	const { data } = event;
	switch (data.operation) {
		case 'addIntellisenseforScriptInclude':
			addIntellisenseforScriptInclude(data.response, monacoEditorInstance);
			break;
		case 'removeIntelliseOfScriptIncludes':
			removeIntelliseOfScriptIncludes();
			break;
		default:
			break;
	}
}

function connectAutoCompleteWorkerWithEditorCacheWorker(monacoEditorInstance) {
	const channelBetweenAutoCompleteWorkerAndEditorCacheWorker = new MessageChannel();
	monacoEditorInstance.autoCompleteWorker.postMessage(
		{
			operation: 'establishConnectionWithEditorCacheWorker',
			portToCommunicateWithEditorCacheWorker: channelBetweenAutoCompleteWorkerAndEditorCacheWorker.port1
		},
		[channelBetweenAutoCompleteWorkerAndEditorCacheWorker.port1]
	);
	monacoEditorInstance.editorCacheWorker.establishConnectionWithAutoCompleteWorker(
		channelBetweenAutoCompleteWorkerAndEditorCacheWorker.port2
	);
}

function addIntellisenseforScriptInclude(response, monacoEditorInstance) {
	const currentModelId = monacoEditorInstance.instance.getModel().id;
	if (currentModelId !== focusedEditor) {
		return;
	}
	for (const scriptIncludeName in response) {
		let { typeDefinition } = response[scriptIncludeName];

		if (!scriptIncludeLibraries[scriptIncludeName]) {
			const scriptIncludeDetails = scriptIncludeName.split('.');
			const scopeName = scriptIncludeDetails.length > 1 ? scriptIncludeDetails[0] : undefined;
			if (scopeName) {
				typeDefinition = `declare namespace ${scopeName} { ${typeDefinition} } ${typeDefinition}`;
			}

			scriptIncludeLibraries[scriptIncludeName] = typeDefinition;

			if (monacoEditorInstance.isAutocompleteEnabled) {
				scriptIncludeDisposables.push(
					monaco.languages.typescript.javascriptDefaults.addExtraLib(typeDefinition, scriptIncludeName)
				);
			}
		}
	}
}

function removeIntelliseOfScriptIncludes() {
	scriptIncludeDisposables.forEach(library => {
		library.dispose();
	});
	scriptIncludeDisposables = [];
	scriptIncludeLibraries = {};
}

function requestScriptIncludeIntellisense(event, monacoEditorInstance) {
	const { text, range } = event.changes[0];
	if (text === '.' || text === '(' || text === '()') {
		const probableScriptIncludeName = determineProbableScriptIncludeName(range, monacoEditorInstance);
		const apisRegisteredForIntellisense = monaco.languages.typescript.javascriptDefaults.getExtraLibs();
		if (!apisRegisteredForIntellisense[probableScriptIncludeName]) {
			delegateFetchingIntellisense(probableScriptIncludeName, monacoEditorInstance.autoCompleteWorker);
		}
	}
}

function determineProbableScriptIncludeName(range, monacoEditorInstance) {
	const { endLineNumber: lineNumber, endColumn: column } = range;
	const model = monacoEditorInstance.instance.getModel();
	const wordAtPosition = model.getWordAtPosition({ lineNumber, column });
	if (wordAtPosition) {
		const { word: probableScriptIncludeName, startColumn } = wordAtPosition;
		const currentLineContent = model.getLineContent(lineNumber);
		let scopeName = monacoEditorInstance.options.scope;
		const contentBeforeCurrentWord = currentLineContent.substring(0, startColumn - 1);
		if (contentBeforeCurrentWord.endsWith('.')) {
			scopeName = model.getWordAtPosition({ lineNumber, column: startColumn - 1 }).word;
		}
		const probableScriptIncludeNameAlongWithScope = `${scopeName}.${probableScriptIncludeName}`;

		return probableScriptIncludeNameAlongWithScope;
	}
}

function delegateFetchingIntellisense(possibleScriptIncludeName, autoCompleteWorker) {
	autoCompleteWorker.postMessage({
		operation: 'requestForTypeDefinition',
		possibleScriptIncludeName
	});
}

export default enableAutoComplete;
