import * as labels from '../../constants/toolbar/toolbarLabels';
import * as actions from '../../actions';
import * as dropDownSections from '../../constants/toolbar/dropDownSections';
import * as dropDownItemTypes from '../../constants/toolbar/dropDownItemTypes';

const generateMoreDropDown = function({
	disableSyntaxHighlighting,
	disableMinimapComponentPreference,
	syntaxHighlightingSelected,
	minimapSelected,
	wordWrapSelected
}) {
	const moreDropDown = {
		[dropDownSections.EDIT_CODE]: {
			label: labels.EDIT_CODE,
			items: []
		},
		[dropDownSections.VIEW]: {
			label: labels.VIEW,
			items: []
		},
		[dropDownSections.SETTINGS]: {
			label: labels.SETTINGS,
			items: [
				{
					type: dropDownItemTypes.TOGGLE,
					disabled: false,
					label: labels.TOGGLE_WORD_WRAP_LABEL,
					action: actions.TOGGLE_WORD_WRAP,
					selected: wordWrapSelected
				}
			]
		},
		[dropDownSections.GET_HELP]: {
			label: labels.GET_HELP,
			items: [
				{
					type: dropDownItemTypes.EXTERNAL,
					disabled: false,
					label: labels.KEYBOARD_SHORTCUTS,
					action: actions.SHOWN_KEYMAP,
					selected: false
				}
			]
		}
	};

	// Add Minimap toggle
	if (!disableMinimapComponentPreference) {
		moreDropDown[dropDownSections.SETTINGS].items.unshift({
			type: dropDownItemTypes.TOGGLE,
			disabled: false,
			label: labels.TOGGLE_MINIMAP_LABEL,
			action: actions.TOGGLE_MINIMAP,
			selected: minimapSelected
		});
	}
	// Add Syntax Hightling
	if (!disableSyntaxHighlighting) {
		moreDropDown[dropDownSections.SETTINGS].items.unshift({
			type: dropDownItemTypes.TOGGLE,
			disabled: false,
			label: labels.TOGGLE_SYNTAX_HIGHLIGHTING_LABEL,
			action: actions.TOGGLE_SYNTAX_HIGHLIGHTING,
			selected: syntaxHighlightingSelected
		});
	}
	return moreDropDown;
};

export default generateMoreDropDown;
