import * as actions from '../../../actions';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { RECORD_TYPE, SYS_ID, SCRIPT_FIELD } from '../../../constants/record';

const LINE_NUMBER = 'lineNumber';
//Creating HttpEffect for breakPoint
const breakPointEffect = createHttpEffect(
	'api/now/js/debugger/breakpoint/:recordType/:sysId/:scriptField/:lineNumber',
	{
		method: 'POST',
		dataParam: 'data',
		pathParams: [RECORD_TYPE, SYS_ID, SCRIPT_FIELD, LINE_NUMBER],
		successActionType: actions.TOGGLE_BREAKPOINT_SUCCEEDED,
		errorActionType: actions.HTTP_EFFECT_RESULTS_ERROR
	}
);

//Creating HttpEffect for logPoint
const logPointEffect = createHttpEffect('api/now/js/logpoints/logpoint/:recordType/:sysId/:scriptField/:lineNumber', {
	method: 'POST',
	dataParam: 'data',
	pathParams: [RECORD_TYPE, SYS_ID, SCRIPT_FIELD, LINE_NUMBER],
	successActionType: actions.TOGGLE_LOGPOINT_SUCCEEDED,
	errorActionType: actions.HTTP_EFFECT_RESULTS_ERROR
});

breakPointEffect.stopPropagation = true;
logPointEffect.stopPropagation = true;

export { breakPointEffect, logPointEffect };
