import { getActiveElement } from '@servicenow/library-enhanced-utils';
import { SPACE, ENTER } from '../now-code-editor/constants/accessibility';

const eventHandlers = [
	{
		events: ['keydown'],
		effect: ({
			action: {
				payload: { event: event }
			}
		}) => {
			if (event.key !== SPACE && event.key !== ENTER) {
				return;
			}
			const activeElement = getActiveElement();
			if (activeElement.classList.contains('external-item')) {
				event.preventDefault();
				activeElement.click();
			}
		}
	}
];

export default eventHandlers;
