import { getSystemPreferenceAsBoolean, getUserPreferenceAsBoolean } from '../helpers/utils';
import { TOGGLE_AUTO_COMPLETE } from '../../../actions';

function autoCompletePreferenceHandler({
	action: {
		payload: { data, errors }
	},
	updateState,
	dispatch
}) {
	if (errors.length === 0 && data) {
		const autoCompleteSystemPreference = getSystemPreferenceAsBoolean(
			data.GlideNowCodeEditor_Query,
			'autoCompleteSystemPreference'
		);
		if (autoCompleteSystemPreference === false) {
			return;
		}
		const autoCompleteUserPreference = getUserPreferenceAsBoolean(
			data.GlideDomain_Query,
			'autoCompleteUserPreference'
		);
		if (autoCompleteUserPreference === true) {
			updateState({ autocompletePreference: true });
			dispatch(TOGGLE_AUTO_COMPLETE);
		}
	}
}

export default autoCompletePreferenceHandler;
