import monaco from '../NowMonaco';
import * as messageSeverities from '../../../constants/messageSeverities';

let severityConf = null;

function getSeverity(severity) {
	if (!severityConf) {
		severityConf = {
			[messageSeverities.ERROR]: monaco.MarkerSeverity.Error,
			[messageSeverities.WARNING]: monaco.MarkerSeverity.Warning,
			[messageSeverities.HINT]: monaco.MarkerSeverity.Hint,
			[messageSeverities.INFO]: monaco.MarkerSeverity.Info
		};
	}
	return severityConf[severity];
}

function getMarkers(model, errors) {
	const markers = errors.map(err => {
		const lineLength = model.getLineLength(err.from.line);
		let startColumn = err.from.column;
		if (startColumn > lineLength) {
			startColumn = lineLength;
		}
		return {
			startLineNumber: err.from.line,
			endLineNumber: err.to.line,
			startColumn,
			endColumn: err.to.column,
			message: err.message,
			severity: getSeverity(err.severity)
		};
	});

	return markers;
}

function updateMarkers(model, markerOwner, errors) {
	const markers = getMarkers(model, errors);
	monaco.editor.setModelMarkers(model, markerOwner, markers);
}

export default updateMarkers;
