import {
	addGlyphHoverWidget,
	showGlyphHoverWidget,
	hideGlyphHoverWidget,
	destroyGlyphHoverWidget
} from './glyphHoverWidget';
import { renderBreakPoints, renderLogPoints } from './renderDebugPoints';
import monaco from '../NowMonaco';
import {
	NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS
} from '../../../constants/cssClasses';
import { Key_Esc } from '../../../constants/keyCodes';

/**
 *
 * @param {Function} gutterClickCommand callback added for the gutter action
 *
 * Based on the type of the event (onMouseDown, onContextMenu, onMouseMove, onDidScrollChange, onMouseLeave)
 * and type of target type and button, we are returning the callback and performing other operations.
 * If the callback is not a function, we are disposing all the events related to debugpoints reistered to the instance
 */

function gutterClickAction(gutterClickCommand) {
	if (typeof gutterClickCommand === 'function') {
		this._glyphHoverOverlayWidget = addGlyphHoverWidget(this.instance);
		let showHoverAtLine;
		this._gutterActions.onMouseDownEvent = this.instance.onMouseDown(e => {
			if (e.target.type === monaco.editor.MouseTargetType.GUTTER_GLYPH_MARGIN && e.event.leftButton) {
				gutterClickCommand(e.target.position.lineNumber);
				showGlyphHoverWidget(this.instance, e.target.position.lineNumber, this._glyphHoverOverlayWidget);
			} else if (e.target.type === monaco.editor.MouseTargetType.OVERLAY_WIDGET && e.event.leftButton) {
				if (e.target.element.classList.contains('now-code-editor-monaco-hover')) {
					hideGlyphHoverWidget(this._glyphHoverOverlayWidget);
					gutterClickCommand(showHoverAtLine);
				} else {
					return;
				}
			} else if (!e.event.rightButton) {
				// we are handling rightclick via onContextMenu event
				gutterClickCommand();
			}
		});
		this._gutterActions.onContextMenuEvent = this.instance.onContextMenu(e => {
			const position = {
				x: e.event.browserEvent.clientX,
				y: e.event.browserEvent.clientY
			};

			if (e.target.type === monaco.editor.MouseTargetType.GUTTER_GLYPH_MARGIN) {
				gutterClickCommand(e.target.position.lineNumber, position);
				e.event.preventDefault();
			} else if (e.target.type === monaco.editor.MouseTargetType.OVERLAY_WIDGET) {
				gutterClickCommand(showHoverAtLine, position);
				e.event.preventDefault();
			}
		});

		this._gutterActions.onDidScrollChangeEvent = this.instance.onDidScrollChange(() => {
			hideGlyphHoverWidget(this._glyphHoverOverlayWidget);
			gutterClickCommand();
		});

		this._gutterActions.onMouseMoveEvent = this.instance.onMouseMove(e => {
			if (e.target.type === monaco.editor.MouseTargetType.GUTTER_GLYPH_MARGIN && !e.target.detail.isAfterLines) {
				const gutterDecorationAtLine = this.debugPointInstance.gutterDecorations[e.target.position.lineNumber];
				if (gutterDecorationAtLine === undefined || gutterDecorationAtLine.length === 0) {
					showHoverAtLine = e.target.position.lineNumber;
					showGlyphHoverWidget(this.instance, e.target.position.lineNumber, this._glyphHoverOverlayWidget);
				} else {
					hideGlyphHoverWidget(this._glyphHoverOverlayWidget);
				}
			} else if (e.target.type === monaco.editor.MouseTargetType.OVERLAY_WIDGET) {
				return;
			} else {
				hideGlyphHoverWidget(this._glyphHoverOverlayWidget);
			}
		});

		this._gutterActions.onMouseLeaveEvent = this.instance.onMouseLeave(() => {
			hideGlyphHoverWidget(this._glyphHoverOverlayWidget);
		});

		this._gutterActions.onKeDownEvent = this.instance.onKeyDown(monacoKeyDown => {
			if (monacoKeyDown.code === Key_Esc && this.debugPointInstance.viewZoneId !== null) {
				const lineNumber = this.debugPointInstance.decoratedLineNumber;
				this.debugPointInstance.removeInputWidget();
				const breakPoints = this.debugPointInstance.breakPoints;
				const logPoints = this.debugPointInstance.logPoints;
				const debugPoint = breakPoints[lineNumber] || logPoints[lineNumber];
				debugPoint
					? this.debugPointInstance.updateDebugPointDecoration(
							lineNumber,
							debugPoint.fEvaluationString,
							breakPoints[lineNumber]
								? debugPoint.fEvaluationString === ''
									? NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS
									: NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS
								: NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS
					  )
					: this.debugPointInstance.removeDebugPointDecoration(lineNumber);
			}
		});

		const model = this.instance.getModel();
		let lineCount = model.getLineCount();
		this._gutterActions.onDidChangeContentEvent = model.onDidChangeContent(() => {
			const newLineCount = model.getLineCount();
			if (newLineCount !== lineCount) {
				lineCount = newLineCount;
				this.debugPointInstance.removeAllDebugPointsDecorations();
				if (this.debugPointInstance.logPoints) {
					renderLogPoints.call(this);
				}
				if (this.debugPointInstance.breakPoints) {
					renderBreakPoints.call(this);
				}
				if (this.debugPointInstance.viewZoneId != null) {
					this.debugPointInstance.addDecorationsToLineWithWidget();
					this.debugPointInstance.layoutZone();
				}
			}
		});
	} else {
		this._glyphHoverOverlayWidget && destroyGlyphHoverWidget(this.instance, this._glyphHoverOverlayWidget);
		this._gutterActions.onMouseDownEvent && this._gutterActions.onMouseDownEvent.dispose();
		this._gutterActions.onContextMenuEvent && this._gutterActions.onContextMenuEvent.dispose();
		this._gutterActions.onMouseLeaveEvent && this._gutterActions.onMouseLeaveEvent.dispose();
		this._gutterActions.onMouseMoveEvent && this._gutterActions.onMouseMoveEvent.dispose();
		this._gutterActions.onDidScrollChangeEvent && this._gutterActions.onDidScrollChangeEvent.dispose();
		this._gutterActions.onKeDownEvent && this._gutterActions.onKeDownEvent.dispose();
		this._gutterActions.onDidChangeContentEvent && this._gutterActions.onDidChangeContentEvent.dispose();
	}
}

export default gutterClickAction;
