import * as actions from '../../actions';
import { RECORD_TYPE, SCRIPT_FIELD, SYS_ID } from '../../constants/record';
import * as queries from './helpers/queries';
import * as handlers from './handlers';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { JAVASCRIPT } from '../../constants/supportedLanguages';
import * as editorConstants from '../../constants/debugPointLabel';

const graphQLEffectHandlers = {
	[actions.DEBUG_POINTS_DATA_REQUEST]: {
		...createGraphQLEffect(queries.debugPointsQuery, {
			templateVarList: [RECORD_TYPE],
			variableList: [RECORD_TYPE, SCRIPT_FIELD, SYS_ID],
			successActionType: actions.DEBUGPOINTS_INITIALIZED,
			errorActionType: actions.EDITOR_DATA_REQUEST_FAILURE
		}),
		stopPropagation: true
	},
	[actions.DEBUGPOINTS_INITIALIZED]: {
		effect(coeffects) {
			handlers.debugPointsHandler(coeffects);
		},
		stopPropagation: true
	},
	[actions.AUTO_COMPLETE_PREFERENCES_DATA_REQUEST]: {
		...createGraphQLEffect(queries.autoCompletePreferencesQuery, {
			variableList: [RECORD_TYPE, SCRIPT_FIELD],
			successActionType: actions.AUTO_COMPLETE_PREFERENCES_INITIALIZED,
			errorActionType: actions.EDITOR_DATA_REQUEST_FAILURE
		}),
		stopPropagation: true
	},
	[actions.AUTO_COMPLETE_PREFERENCES_INITIALIZED]: {
		effect(coeffects) {
			handlers.autoCompletePreferenceHandler(coeffects);
		},
		stopPropagation: true
	},
	[actions.MACROS_DATA_REQUEST]: {
		...createGraphQLEffect(queries.macrosQuery, {
			successActionType: actions.MACROS_INITIALIZED,
			errorActionType: actions.EDITOR_DATA_REQUEST_FAILURE
		}),
		stopPropagation: true
	},
	[actions.MACROS_INITIALIZED]: {
		effect(coeffects) {
			handlers.macrosHandler(coeffects);
		},
		stopPropagation: true
	},
	[actions.LINT_CONFIG_DATA_REQUEST]: {
		...createGraphQLEffect(queries.lintConfigQuery, {
			successActionType: actions.LINT_CONFIG_INITIALIZED,
			errorActionType: actions.EDITOR_DATA_REQUEST_FAILURE
		}),
		stopPropagation: true
	},
	[actions.LINT_CONFIG_INITIALIZED]: {
		effect(coeffects) {
			handlers.lintingHandler(coeffects);
		},
		stopPropagation: true
	},
	[actions.TOGGLE_LINTING]: {
		effect(coeffects) {
			const {
				state: { editor, isLintingEnabled, systemEslintConfig },
				properties: {
					language,
					linting: {
						lintConfig: lintConfigComponentPreference,
						disabled: lintDisabledComponentPreference,
						linterUrl: linterUrlComponentPreference
					}
				}
			} = coeffects;
			if (language === JAVASCRIPT && lintDisabledComponentPreference === false && isLintingEnabled) {
				if (lintConfigComponentPreference !== undefined) {
					editor.initLint(lintConfigComponentPreference, linterUrlComponentPreference);
				} else {
					editor.initLint(systemEslintConfig, linterUrlComponentPreference);
				}
			} else {
				editor.disableLint();
			}
		},
		stopPropagation: true
	},
	[actions.API_DOC_RESOURCES_DATA_REQUEST]: {
		...createGraphQLEffect(queries.apiDocResourcesQuery, {
			variableList: [RECORD_TYPE, SCRIPT_FIELD],
			successActionType: actions.TOKENS_INITIALIZED,
			errorActionType: actions.EDITOR_DATA_REQUEST_FAILURE
		}),
		stopPropagation: true
	},

	[actions.FLUSH_TIMES_DATA_REQUEST]: {
		...createGraphQLEffect(queries.flushTimesQuery, {
			successActionType: actions.FLUSH_TIMES_INITIALIZED,
			errorActionType: actions.EDITOR_DATA_REQUEST_FAILURE
		}),
		stopPropagation: true
	},

	[actions.FLUSH_TIMES_INITIALIZED]: {
		effect(coeffects) {
			handlers.flushTimesHandler(coeffects);
		},
		stopPropagation: true
	},

	[actions.TOKENS_INITIALIZED]: {
		effect(coeffects) {
			handlers.apiDocResourcesHandler(coeffects);
		},
		stopPropagation: true
	},
	[actions.EDITOR_DATA_REQUEST_FAILURE]: {
		effect(coeffects) {
			//TODO:
		},
		stopPropagation: true
	},
	[actions.TOGGLE_DEBUGGING]: {
		effect(coeffects) {
			const {
				action: {
					payload: { logPointRole, scriptDebuggerRole, logPointSystemPreference }
				},
				properties: {
					disableDebugging: disableDebuggingComponentPreference,
					isClientScript,
					hideGutter,
					language
				},
				state: { editor, logPoints, breakPoints },
				dispatch,
				updateState
			} = coeffects;
			editor.updateGutterOptions();

			if (
				language === JAVASCRIPT &&
				hideGutter === false &&
				isClientScript === false &&
				disableDebuggingComponentPreference === false
			) {
				editor.renderDebugPoints({ breakPoints, logPoints });
				editor.registerGutterClick((lineNumber, position) => {
					dispatch(actions.GUTTER_CLICKED, {
						lineNumber,
						position,
						logPointRole,
						scriptDebuggerRole,
						logPointSystemPreference
					});
				});

				updateState({ ambSubscribed: true });

				const filter = btoa(editorConstants.DEBUGPOINTS_FILTER).replace(/=/g, '-');
				dispatch(actions.BREAKPOINTS_AMB_SUBSCRIPTION_REQUEST, {
					table: editorConstants.BREAKPOINTS_TABLE_NAME,
					filter,
					subscribe: true
				});
				dispatch(actions.LOGPOINTS_AMB_SUBSCRIPTION_REQUEST, {
					table: editorConstants.LOGPOINTS_TABLE_NAME,
					filter,
					subscribe: true
				});
			} else {
				editor.unBindDebugging();
				updateState({ ambSubscribed: false });
			}
		},
		stopPropagation: true
	},

	[actions.TOGGLE_AUTO_COMPLETE]: {
		effect({
			properties: {
				autocompleteSuggestions: { disabled: disableAutocompleteComponentPreference },
				readOnly: readOnlyComponentPreference,
				language
			},
			state: { editor, autocompletePreference, apisDocResourceFlushTime }
		}) {
			const isAutoCompleteEnabled = !disableAutocompleteComponentPreference && autocompletePreference;

			if (language === JAVASCRIPT && !readOnlyComponentPreference) {
				editor.enableAutoComplete(apisDocResourceFlushTime, isAutoCompleteEnabled);
			}
		},
		stopPropagation: true
	},

	[actions.TOGGLE_DISCOVERABILITY]: {
		effect({
			state: { editor, contextmenuPreference, apisDocResourceFlushTime, apisDocResourceIds },
			properties: { language, disableContextmenu: disableContextmenuComponentPreference }
		}) {
			const isDiscoverabilityEnabled = !disableContextmenuComponentPreference && contextmenuPreference;

			//Removes all the discovered decorations on editor
			editor.updateDiscoveredTokensOnEditor();

			if (language === JAVASCRIPT && isDiscoverabilityEnabled) {
				editor.enableDiscoverability(apisDocResourceFlushTime, apisDocResourceIds);
			}
		},
		stopPropagation: true
	},

	[actions.DEBUG_POINTS_DATA_REQUEST_AMB]: {
		...createGraphQLEffect(queries.debugPointsQuery, {
			variableList: [RECORD_TYPE, SCRIPT_FIELD, SYS_ID],
			successActionType: actions.DEBUGPOINTS_RECEIVED,
			errorActionType: actions.EDITOR_DATA_REQUEST_FAILURE
		}),
		stopPropagation: true
	},
	[actions.DEBUGPOINTS_RECEIVED]: {
		effect(coeffects) {
			handlers.debugPointsAMBHandler(coeffects);
		},
		stopPropagation: true
	}
};

export default graphQLEffectHandlers;
