import * as toolbarComponents from '../../../now-code-editor/constants/toolbar/toolbarComponents';
import '@servicenow/now-popover';
import '../../../sn-code-editor-toolbar-dropdown';
import { t } from 'sn-translate';

function renderControl({ control, moreDropDownData, index }) {
	const tabIndex = index === 0 ? null : '-1';
	switch (control.componentName) {
		case toolbarComponents.NOW_BUTTON_STATEFUL:
			return (
				<now-button-stateful
					tabindex={tabIndex}
					manage-selected
					icon={control.icon}
					tooltipContent={control.tooltip}
					size="md"
					variant="tertiary"
					disabled={control.disabled}
					selected={control.selected}
					config-aria={{
						button: { 'aria-label': control.tooltip }
					}}
					append-to-payload={{ action: control.action }}
				></now-button-stateful>
			);
		case toolbarComponents.NOW_BUTTON_ICONIC:
			return (
				<now-button-iconic
					tabindex={tabIndex}
					icon={control.icon}
					tooltipContent={control.tooltip}
					size="md"
					variant="tertiary"
					disabled={control.disabled}
					bare={true}
					config-aria={{
						button: { 'aria-label': control.tooltip }
					}}
					append-to-payload={{ action: control.action }}
				></now-button-iconic>
			);
		case toolbarComponents.NOW_POPOVER:
			return (
				<now-popover
					hide-tail
					manage-opened
					opened={control.selected}
					positions={[
						{ target: 'bottom-end', content: 'top-end' },
						{ target: 'bottom-start', content: 'top-start' }
					]}
				>
					<now-button-iconic
						tabindex={tabIndex}
						icon={control.icon}
						tooltipContent={control.tooltip}
						size="md"
						variant="tertiary"
						disabled={control.disabled}
						bare={true}
						config-aria={{
							button: { 'aria-label': control.tooltip, 'aria-haspopup': 'true' }
						}}
						append-to-payload={{ action: control.action }}
						slot="trigger"
					></now-button-iconic>
					<sn-code-editor-toolbar-dropdown slot="content" dropDownItems={moreDropDownData} />
				</now-popover>
			);
		default:
			return;
	}
}

function renderSections({ sections, sectionName, moreDropDownData }) {
	let sectionsView = '';
	if (sections && sections.length > 0) {
		const sectionsReducer = (allSections, currentSection) => {
			if (currentSection.length > 0) {
				const ariaLabel =
					currentSection.length > 1
						? t(
								'{0} controls in toolbar. To navigate items on this section press, Control-Option, and then Left Arrow, Right Arrow',
								sectionName
						  )
						: '';
				allSections.push(
					<div className={`sn-code-editor-toolbar-section`} aria-label={ariaLabel}>
						{currentSection.map((control, index) => {
							return (
								<div className={`sn-code-editor-control`} id={`${control.icon}-${index}`}>
									{renderControl({ control, moreDropDownData, index })}
								</div>
							);
						})}
					</div>
				);
			}
			return allSections;
		};
		sectionsView = sections.reduce(sectionsReducer, []);
	}
	return sectionsView;
}

export default renderSections;
