import { SYS_DB_OBJECT, SYS_SCRIPT_INCLUDE } from '../../../constants/contextMenu';

function flushTimesHandler({
	action: {
		payload: { data, errors }
	},
	updateState
}) {
	if (errors.length === 0 && data && data.GlideNowCodeEditor_Query && data.GlideNowCodeEditor_Query.flushTimes) {
		const editorResources = data.GlideNowCodeEditor_Query.flushTimes;
		const apisDocResourceFlushTime = {
			[SYS_DB_OBJECT]: editorResources.sysDbObjectFlushTime,
			[SYS_SCRIPT_INCLUDE]: editorResources.sysScriptIncludeFlushTime
		};

		updateState({ apisDocResourceFlushTime });
	}
}
export default flushTimesHandler;
