import * as _ from 'lodash';
import {
	TOGGLE_LINTING,
	TOGGLE_DEBUGGING,
	TOGGLE_AUTO_COMPLETE,
	REGISTER_ON_CONTENT_CHANGED,
	TOGGLE_DISCOVERABILITY,
	DEBUG_POINTS_DATA_REQUEST
} from '../../../actions';
import { dispatchGraphQLRequests } from '../../graphQLEffects/helpers/utils';
import { JAVASCRIPT, PLAIN_TEXT } from '../../../constants/supportedLanguages';

const dispatchGraphQLRequestsDebounced = _.debounce(dispatchGraphQLRequests, 100);

const updateProperty = coeffects => {
	const {
		host,
		state: { editor },
		properties,
		action: { payload },
		dispatch,
		updateState
	} = coeffects;

	const { name: propertyName, value: newValue, previousValue } = payload;

	editor.updateOptions(propertyName, newValue);

	switch (propertyName) {
		case 'content': {
			editor.discoverTokens();
			editor.updateEditorOptions(propertyName, newValue);
			break;
		}
		case 'cursorPosition': {
			editor.setCursorPosition(newValue);
			break;
		}
		case 'language': {
			editor.setLanguage(properties.disableSyntaxHighlighting ? PLAIN_TEXT : newValue);

			if (newValue == JAVASCRIPT) {
				dispatchGraphQLRequestsDebounced(properties, dispatch);
			} else {
				dispatch(TOGGLE_DEBUGGING, {});
				dispatch(TOGGLE_AUTO_COMPLETE);
				dispatch(TOGGLE_LINTING);
			}
			break;
		}
		case 'recordType':
		case 'scriptField':
		case 'sysId': {
			if (properties.language === JAVASCRIPT) {
				dispatchGraphQLRequestsDebounced(properties, dispatch);
			}
			break;
		}
		case 'readOnly': {
			editor.updateEditorOptions(propertyName, newValue);
			if (!newValue) {
				const { recordType, scriptField, sysId } = properties;
				dispatch(DEBUG_POINTS_DATA_REQUEST, { recordType, scriptField, sysId });
				dispatch(TOGGLE_AUTO_COMPLETE);
				dispatch(REGISTER_ON_CONTENT_CHANGED);
			}
			break;
		}
		case 'linting': {
			if (!_.isEqual(newValue, previousValue)) {
				dispatch(TOGGLE_LINTING);
			}
			break;
		}
		case 'disableDebugging':
		case 'hideGutter': {
			if (newValue === false) {
				const { recordType, scriptField, sysId } = properties;
				dispatch(DEBUG_POINTS_DATA_REQUEST, { recordType, scriptField, sysId });
			} else {
				dispatch(TOGGLE_DEBUGGING, {});
			}
			break;
		}
		case 'isClientScript': {
			if (newValue === false) {
				const { recordType, scriptField, sysId } = properties;
				dispatch(DEBUG_POINTS_DATA_REQUEST, { recordType, scriptField, sysId });
			} else {
				dispatch(TOGGLE_DEBUGGING, {});
			}
			dispatch(TOGGLE_AUTO_COMPLETE);
			break;
		}
		case 'theme': {
			editor.updateEditorOptions(propertyName, newValue);
			break;
		}
		case 'width': {
			host.style.width = newValue;
			break;
		}
		case 'height': {
			host.style.height = newValue;
			break;
		}
		case 'autocompleteSuggestions': {
			if (!_.isEqual(newValue, previousValue)) {
				dispatch(TOGGLE_AUTO_COMPLETE);
			}
			break;
		}
		case 'disableSyntaxHighlighting': {
			if (newValue) {
				editor.setLanguage(PLAIN_TEXT);
			} else {
				editor.setLanguage(properties.language);
			}
			return;
		}
		case 'disableMinimap': {
			editor.enableMinimap(!newValue);
			updateState({ minimapSelected: !newValue });
			return;
		}
		case 'disableContextmenu': {
			dispatch(TOGGLE_DISCOVERABILITY);
			break;
		}
		case 'scope': {
			editor.discoverTokens();
			break;
		}

		case 'markers': {
			if (!_.isEqual(newValue, previousValue)) {
				editor.updateEditorMarkers(newValue);
				break;
			}
		}
	}
};

export default updateProperty;
