import * as actions from '../../../actions';

function dispatchGraphQLRequests(properties, dispatch) {
	const { recordType, scriptField, sysId } = properties;

	dispatch(actions.FLUSH_TIMES_DATA_REQUEST);
	dispatch(actions.API_DOC_RESOURCES_DATA_REQUEST, { recordType, scriptField });
	dispatch(actions.AUTO_COMPLETE_PREFERENCES_DATA_REQUEST, { recordType, scriptField });
	dispatch(actions.DEBUG_POINTS_DATA_REQUEST, { recordType, scriptField, sysId });
	dispatch(actions.MACROS_DATA_REQUEST);
	dispatch(actions.LINT_CONFIG_DATA_REQUEST);
}

function getComputedUserPreference(userPreference) {
	if (Array.isArray(userPreference) === false || userPreference.length === 0) {
		return true;
	}
	const value = userPreference[0].value;
	if (value === false || value === 'false') {
		return false;
	}

	return true;
}

function getSystemPreferenceAsBoolean(systemPreferenceResponse, desiredSystemPreference) {
	if (systemPreferenceResponse && systemPreferenceResponse[desiredSystemPreference]) {
		const value = systemPreferenceResponse[desiredSystemPreference].value;
		if (value === false || value === 'false') {
			return false;
		}
	}
	return true;
}

function getUserPreferenceAsBoolean(userPreferenceResponse, desiredUserPreference) {
	if (userPreferenceResponse && userPreferenceResponse.user && userPreferenceResponse.user[desiredUserPreference]) {
		const userPreferenceValue = getComputedUserPreference(userPreferenceResponse.user[desiredUserPreference]);
		return userPreferenceValue;
	}

	return true;
}

export { dispatchGraphQLRequests, getSystemPreferenceAsBoolean, getUserPreferenceAsBoolean };
