import monaco from '../NowMonaco';
import beautify_xml from 'xml-beautifier';
import { css as beautify_css, js as beautify_js } from 'js-beautify';
import { JAVASCRIPT, XML, CSS } from '../../../constants/supportedLanguages';

function beautifier(language) {
	switch (language) {
		case JAVASCRIPT:
			return beautify_js;
		case XML:
			return beautify_xml;
		case CSS:
			return beautify_css;
		default:
			return code => code;
	}
}

function beautifyCode(editor, language) {
	let selection = editor.getSelection();
	const model = editor.getModel();
	if (selection.isEmpty()) {
		selection = new monaco.Range(0, 1, model.getLineCount(), model.getLineMaxColumn(model.getLineCount()));
	}
	const content = beautifier(language)(model.getValueInRange(selection));
	editor.executeEdits('fomatCode', [
		{
			range: selection,
			text: content
		}
	]);
}

export default beautifyCode;
