import { getActiveElement } from '@servicenow/library-enhanced-utils';
import * as accessibilityConstant from '../now-code-editor/constants/accessibility';

const moveControl = (moveDirection, moveToStart, moveToEnd) => {
	try {
		const element = getActiveElement();
		const host = element.parentNode.host;
		const hostParentId = host.parentNode.getAttribute('id');
		const currentIndex = parseInt(hostParentId.substring(hostParentId.lastIndexOf('-') + 1));
		const length = host.parentNode.parentNode.children.length;

		if (isNaN(currentIndex)) {
			return;
		}
		let nextIndex = 0;
		if (moveDirection === accessibilityConstant.MOVE_RIGHT) {
			nextIndex = moveToEnd ? length - 1 : currentIndex + 1;
		}
		if (moveDirection === accessibilityConstant.MOVE_LEFT) {
			nextIndex = moveToStart ? 0 : currentIndex - 1;
		}
		if (nextIndex === length || nextIndex === -1) {
			return;
		}
		const elementToFocus = host.parentNode.parentNode.children[nextIndex].firstChild;

		/**
		 * 1.	Removeing tabIndex attribute so that next time focus come to this element when user do Tab and Shift-tab.
		 * 2.	Adding  the tabIndex to current host element so that the focus does't come to this element when user do Tab and Shift-tab
		 * With this we are ensuring only one element within the group can get focus with tab and all other get focus using arrow key (left and right)
		 * */
		elementToFocus.removeAttribute('tabIndex');
		elementToFocus.focus();
		host.setAttribute('tabIndex', '-1');
	} catch (e) {}
};
const eventHandlers = [
	{
		events: ['keydown'],
		effect: ({
			action: {
				payload: { event: event }
			}
		}) => {
			if (event.key === accessibilityConstant.ARROW_RIGHT) {
				event.preventDefault();
				moveControl(accessibilityConstant.MOVE_RIGHT, false, false);
			}
			if (event.key === accessibilityConstant.ARROW_LEFT) {
				event.preventDefault();
				moveControl(accessibilityConstant.MOVE_LEFT, false, false);
			}
			if (event.key === accessibilityConstant.HOME) {
				event.preventDefault();
				moveControl(accessibilityConstant.MOVE_LEFT, true, false);
			}
			if (event.key === accessibilityConstant.END) {
				event.preventDefault();
				moveControl(accessibilityConstant.MOVE_RIGHT, false, true);
			}
		}
	}
];

export default eventHandlers;
