import monaco from '../NowMonaco';
import { JAVASCRIPT } from '../../../constants/supportedLanguages';

/**
 *
 * @param {number} startLineNumber
 *
 * getEditorTokens would return the tokens for the script starting from the line startLineNumber till end of file
 */
function getEditorTokens(startLineNumber) {
	try {
		const lines = [];
		const model = this.instance.getModel();
		const lineCount = model.getLineCount();

		startLineNumber = isNaN(startLineNumber) ? 1 : startLineNumber;

		for (let i = startLineNumber; i <= lineCount; i++) {
			const editorLine = model.getLineContent(i);
			const lineTokens = monaco.editor.tokenize(editorLine, JAVASCRIPT)[0];
			lines.push(
				lineTokens.map((item, index) => {
					const startIndex = item.offset;
					const endIndex = lineTokens[index + 1] && lineTokens[index + 1].offset;
					const string = editorLine.substring(startIndex, endIndex);
					return {
						line: i,
						string,
						type: item.type,
						startColumn: startIndex + 1,
						endColumn: (endIndex ? endIndex : startIndex + string.length) + 1
					};
				})
			);
		}
		return lines;
	} catch (e) {}
}

/**
 *
 * @param {{}} result Discovered tokens
 *
 * updateTokensOnEditor paints the CSS on editor for the discovered tokens (result)
 */
function updateTokensOnEditor(result) {
	try {
		const tokens = result.tokens || [];
		const decorations = [];

		for (let i = 0; i < tokens.length; i++) {
			for (let j = 0; j < tokens[i].length; j++) {
				const discoverableToken = tokens[i][j];
				const token = discoverableToken.token;

				decorations.push({
					//Range is an monaco object which holds the info of startLine,startColumn,endLine,endColumn
					range: new monaco.Range(token.line, token.startColumn, token.line, token.endColumn),
					options: {
						inlineClassName:
							'now-code-editor-discoverable-text ' +
							(discoverableToken.type + '-' + discoverableToken.key)
					}
				});
			}
		}
		this.discoverDecorations = this.instance.deltaDecorations(this.discoverDecorations, decorations);
	} catch (e) {}
}

/**
 * registerDiscoverEvents registers onContentChange event on editor and debounces the callback to 250 milliseconds
 */

function registerDiscoverEvents() {
	this.instance.getModel().onDidChangeContent(
		onContentChangeDebounce(startLineNumber => {
			this.discoverTokens(startLineNumber);
		}, 250)
	);
}

/**
 * onContentChangeDebounce is the onContentChange event debouncing implementation
 */
function onContentChangeDebounce(func, delay) {
	let debounceTimer;
	let startLine;
	return event => {
		const editedLine = getFirstEditedLine(event);
		startLine = startLine < editedLine ? startLine : editedLine;
		clearTimeout(debounceTimer);
		debounceTimer = setTimeout(() => {
			func(startLine);
			startLine = undefined;
		}, delay);
	};
}

/**
 * getFirstEditedLine would return you the first edited line in an event
 */
function getFirstEditedLine(event) {
	const { changes } = event;
	if (changes.length > 0) {
		const change = changes[changes.length - 1];
		const {
			range: { startLineNumber }
		} = change;
		return startLineNumber;
	}
	return 1;
}

export { getEditorTokens, updateTokensOnEditor, registerDiscoverEvents };
