import * as actions from '../../../actions';
function initializeKeyBindings(editor, dispatch) {
	editor.addHelpCommand(() => {
		dispatch(actions.SHOWN_KEYMAP);
	});

	editor.addFullscreenCommand(() => {
		dispatch(actions.TOGGLED_FULLSCREEN);
	});

	editor.addSyntaxCheckCommand(() => {
		dispatch(actions.TOGGLED_SYNTAX_CHECK);
	});

	editor.addFormatCodeCommand(() => {
		dispatch(actions.FORMATED_CODE);
	});
}

export default initializeKeyBindings;
