import { getSystemPreferenceAsBoolean } from '../helpers/utils';
import { TOGGLE_DISCOVERABILITY } from '../../../actions';

function apiDocResourcesHandler({
	action: {
		payload: { data, errors }
	},
	updateState,
	dispatch
}) {
	if (errors.length === 0 && data && data.GlideNowCodeEditor_Query && data.GlideNowCodeEditor_Query.editorResources) {
		const editorResources = data.GlideNowCodeEditor_Query.editorResources;
		let apisDocResourceIds = {};
		let contextmenuPreference = false;

		const contextMenuSystemPreference = getSystemPreferenceAsBoolean(
			data.GlideNowCodeEditor_Query,
			'contextMenuSystemPreference'
		);
		if (contextMenuSystemPreference === true) {
			try {
				if (editorResources.apisDocResourceIds !== '') {
					apisDocResourceIds = JSON.parse(editorResources.apisDocResourceIds || '{}');
				}
				contextmenuPreference = true;
			} catch (error) {}
		}

		updateState({ apisDocResourceIds, contextmenuPreference });
		dispatch(TOGGLE_DISCOVERABILITY);
	}
}

export default apiDocResourcesHandler;
