import monaco from '../NowMonaco';
import { NOW_CODE_EDITOR_DEBUGPOINT_MARKER_CLASS } from '../../../constants/cssClasses';
import { LOGPOINT_PLACEHOLDER, BREAKPOINT_PLACEHOLDER, LOGPOINT_ACTION } from '../../../constants/debugPointLabel';

/**
 *
 * @param {{}} editor editor where we are using debugPoint service
 * @param {Array} gutterDecorations decorations added to the editor gutter
 *
 * returns editor,
 * gutterDecorations,
 * overlayWidget : debugpoint widget added to the editor,
 * viewZoneId: zone id of the debugpoint widget added to the editor,
 * decoratedLineNumber: linenumber where debugpoint widge was added,
 * logPoints: list of all the logpoints
 * breakPoints: list of all the breakpoints
 * domeNode: domeNode for the overlayWidget
 */
export default class DebugService {
	constructor(editor, gutterDecorations) {
		DebugService.instance = this;
		this.editor = editor;
		this.gutterDecorations = gutterDecorations;
		this.overlayWidget = {};
		this.viewZoneId = null;
		this.domNode = null;
		this.decoratedLineNumber = null;
		this.logPoints = null;
		this.breakPoints = null;
		return this;
	}

	/**
	 *
	 * @param {{}} breakPoints All the breakpoints available
	 * update breakPoints
	 *
	 */
	updateBreakPoints(breakPoints) {
		this.breakPoints = breakPoints;
	}

	/**
	 *
	 * @param {{}} logPoints All the logPoints available
	 * update logPoints
	 *
	 */
	updateLogPoints(logPoints) {
		this.logPoints = logPoints;
	}

	setFocus() {
		this.editor.focus();
	}

	/**
	 *
	 * @param {Number} lineNumber lineNumber where to add/update debugpoint
	 * @param {String} evaluationString condition for conditionalbreakpoint or logpoint evaluations string
	 * @param {String} className {NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS/ NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS/ NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS}
	 *  based on type of debugpoint getting added/updated
	 */
	updateDebugPointDecoration(lineNumber, evaluationString, className) {
		this.gutterDecorations[lineNumber] = this.editor.deltaDecorations(this.gutterDecorations[lineNumber] || [], [
			{
				range: new monaco.Range(lineNumber, 1, lineNumber, 1),
				options: {
					glyphMarginClassName: className,
					glyphMarginHoverMessage: { value: evaluationString }
				}
			}
		]);
	}

	/**
	 *
	 * @param {Number} lineNumber lineNumber where to remove debugpoint
	 *
	 */
	removeDebugPointDecoration(lineNumber) {
		this.gutterDecorations[lineNumber] = this.editor.deltaDecorations(this.gutterDecorations[lineNumber], []);
	}

	// removes all the debugPoints from the view
	removeAllDebugPointsDecorations() {
		this.gutterDecorations = this.editor.deltaDecorations(this.gutterDecorations, []);
	}

	/**
	 *
	 * @param {Number} lineNumber lineNumber where to remove debugpoint
	 * @param {String} data is the evaluations String added to the debugpoint
	 */
	addInputWidget(lineNumber, data, action) {
		this.editor.updateOptions({
			scrollbar: {
				handleMouseWheel: false
			}
		});
		this.decoratedLineNumber = lineNumber;

		this.editor.changeViewZones(changeAccessor => {
			const ZonedomNode = document.createElement('div');
			const viewZone = new ViewZoneDelegate(ZonedomNode, lineNumber, 31, top => this._onViewZoneTop(top));
			this.viewZoneId = changeAccessor.addZone(viewZone);
			const layoutInfo = this.editor.getLayoutInfo();
			const width = layoutInfo.contentWidth;
			const left = layoutInfo.contentLeft;
			this.overlayWidget = {
				domNode: null,
				getId: function() {
					return 'now-code-editor-input-widget';
				},
				getDomNode: function() {
					let domNode = null;
					if (!this.domNode) {
						domNode = document.createElement('input');
						domNode.placeholder = action == LOGPOINT_ACTION ? LOGPOINT_PLACEHOLDER : BREAKPOINT_PLACEHOLDER;
						domNode.maxLength = 255;
						domNode.value = data;
						domNode.classList.add('now-code-editor-input-widget');
						domNode.style.width = `${width - 14}px`;
						domNode.style.left = `${left}px`;
					}
					this.domNode = this.domNode || domNode;
					return this.domNode;
				},
				getPosition: function() {
					return null;
				}
			};
			this.editor.addOverlayWidget(this.overlayWidget);
			this.domNode = this.overlayWidget.getDomNode();
		});

		this.editor.onDidLayoutChange(() => {
			const layoutInfo = this.editor.getLayoutInfo();
			this.domNode.style.width = `${layoutInfo.contentWidth - 14}px`;
		});

		this.addDecorationsToLineWithWidget();
	}

	/**
	 *
	 * @param {Number} top
	 * set the top for the viewzone
	 */
	_onViewZoneTop(top) {
		this.domNode.style.top = top + 'px';
	}

	/**
	 * add decorations to line for which we need to set the conditional breakPoint/ logpoints
	 */
	addDecorationsToLineWithWidget() {
		const lineNumber = this.decoratedLineNumber;
		// we are adding a background to the line for which we add the debugpoint
		this.gutterDecorations[lineNumber] = this.editor.deltaDecorations(this.gutterDecorations[lineNumber] || [], [
			{
				range: new monaco.Range(lineNumber, 1, lineNumber, 1),
				options: {
					isWholeLine: true,
					className: 'now-code-editor-debugpoint-content',
					glyphMarginClassName: NOW_CODE_EDITOR_DEBUGPOINT_MARKER_CLASS
				}
			}
		]);
	}

	/**
	 * layouts the zone for the widget
	 */
	layoutZone() {
		this.editor.changeViewZones(changeAccessor => {
			changeAccessor.layoutZone(this.viewZoneId);
		});
	}

	//This method removed Input widget if there is any opened on editor
	removeInputWidget() {
		this.editor.removeOverlayWidget(this.overlayWidget);
		this.editor.changeViewZones(changeAccessor => {
			changeAccessor.removeZone(this.viewZoneId);
		});
		this.viewZoneId = null;
		this.editor.updateOptions({
			scrollbar: {
				handleMouseWheel: true
			}
		});
		setTimeout(() => this.setFocus(), 0);
	}
}

class ViewZoneDelegate {
	constructor(domNode, afterLineNumber, heightInPx, onDomNodeTop) {
		this.domNode = domNode;
		this.afterLineNumber = afterLineNumber;
		this.heightInPx = heightInPx;
		this._onDomNodeTop = onDomNodeTop;
	}

	onDomNodeTop(top) {
		this._onDomNodeTop(top);
	}
}
