import keyBoardShortcutsConfig from '../../constants/toolbar/keyBoardShortcutsConfig';
import { JAVASCRIPT, JSON } from '../../constants/supportedLanguages';

function getKeyBoardShortcuts({
	language,
	disableLintingComponentPreference,
	disableFullScreenComponentPreference,
	disableDebuggingComponentPreference,
	hideGutterComponentPreference,
	isClientScript
}) {
	const keymap = [];

	keymap.push(keyBoardShortcutsConfig.find);
	keymap.push(keyBoardShortcutsConfig.findAndReplace);

	if (language !== JSON) {
		keymap.push(keyBoardShortcutsConfig.toggleComment);
	}

	keymap.push(keyBoardShortcutsConfig.formatCode);

	if (disableLintingComponentPreference === false && language === JAVASCRIPT) {
		keymap.push(keyBoardShortcutsConfig.toggleCheckSyntax);
	}

	if (!disableFullScreenComponentPreference) {
		keymap.push(keyBoardShortcutsConfig.toggleFullScreen);
	}
	if (
		disableDebuggingComponentPreference === false &&
		hideGutterComponentPreference === false &&
		isClientScript === false &&
		language === JAVASCRIPT
	) {
		keymap.push(keyBoardShortcutsConfig.manageBreakpoint);
	}
	keymap.push(keyBoardShortcutsConfig.showCommandPalette);
	keymap.push(keyBoardShortcutsConfig.toggleTabKeyMoveFocus);
	keymap.push(keyBoardShortcutsConfig.showSuggestions);

	return keymap;
}

export default getKeyBoardShortcuts;
