import { SYS_SCRIPT_INCLUDE, SYS_DB_OBJECT, API } from '../../constants/contextMenu';

let discoverInstance = null;
/**
 * discoverInit creates a shared worker instance and returns establishConnectionWithEditorCacheWorker, discoverTokens and destroy methods
 */
function discoverInit() {
	if (typeof window.SharedWorker === 'undefined') {
		return;
	}

	const callbacks = {};
	let discoveredTokens = [];

	let discoverWorker = new SharedWorker('../../../workers/discoverWorker');
	let workerPort = discoverWorker.port;

	workerPort.onmessage = (message = {}) => {
		try {
			const data = message.data || {};
			const id = data.id || '';
			const command = data.command || '';

			if (command === 'discover') {
				const newlyIdentifiedTokens = data.result.tokens;
				discoveredTokens.push(...newlyIdentifiedTokens);
				data.result.tokens = discoveredTokens;
			}

			/**
			 * Main context would execute the callbacks on worker response and removes the existing callbacks on main context
			 */
			if (callbacks[command] && callbacks[command][id]) {
				callbacks[command][id].callback && callbacks[command][id].callback(data.result || {});
			}
			removeCallback(data.command, data.id);
		} catch (e) {}
	};

	function addCallback(command, callback) {
		try {
			callbacks[command] = callbacks[command] || {};

			const id = Math.floor(Math.random() * 1001);
			callbacks[command][id] = {
				callback: callback,
				start: Date.now()
			};
			return id;
		} catch (e) {}
	}

	function removeCallback(command, id) {
		try {
			if (callbacks[command]) {
				delete callbacks[command][id];
			}
		} catch (e) {}
	}

	/**
	 *
	 * @param {number} startLineNumber
	 *
	 * removeUnprocessedTokens removes the tokens from discoveredTokens which are after start line
	 */
	function removeUnprocessedTokens(startLineNumber) {
		if (startLineNumber === 1) {
			discoveredTokens = [];
			return;
		}

		discoveredTokens.length = findIndex(startLineNumber);
	}

	function findIndex(startLineNumber) {
		let low = 0;
		let high = discoveredTokens.length - 1;

		while (low <= high) {
			const mid = Math.floor((low + high) / 2);
			const lineNumber = discoveredTokens[mid][0].token.line;
			if (lineNumber === startLineNumber) {
				return mid;
			} else if (lineNumber < startLineNumber) {
				low = mid + 1;
			} else {
				high = mid - 1;
			}
		}
		return low;
	}

	discoverInstance = {
		establishConnectionWithEditorCacheWorker: portToCommunicateWithEditorCacheWorker => {
			workerPort.postMessage(
				{
					command: 'establishConnectionWithEditorCacheWorker',
					portToCommunicateWithEditorCacheWorker
				},
				[portToCommunicateWithEditorCacheWorker]
			);
		},
		discoverTokens: (apisDocResourceIds, lines = [], startLineNumber, options, callback) => {
			try {
				const command = 'discover';
				const id = addCallback(command, callback);
				removeUnprocessedTokens(startLineNumber);

				workerPort.postMessage({
					apisDocResourceIds,
					command,
					id,
					lines,
					currentScope: options.currentScope || ''
				});
			} catch (e) {}
		},
		destroy: () => {
			try {
				if (workerPort) {
					workerPort.close && workerPort.close();
					discoverWorker = workerPort = null;
				}
			} catch (e) {}
		}
	};

	return discoverInstance;
}

function connectDiscoverWorkerWithEditorCacheWorker(editorCacheReloaded) {
	if (this.discoverWorker && this.editorCacheWorker) {
		const channelBetweenDiscoverWorkerAndEditorCacheWorker = new MessageChannel();
		this.discoverWorker.establishConnectionWithEditorCacheWorker(
			channelBetweenDiscoverWorkerAndEditorCacheWorker.port1
		);
		this.editorCacheWorker.establishConnectionWithDiscoverWorker(
			{
				portToCommunicateWithDiscoverWorker: channelBetweenDiscoverWorkerAndEditorCacheWorker.port2,
				editorCacheReloaded
			},
			registerDiscoverabilityEvents.bind(this)
		);
	}
}

function registerDiscoverabilityEvents() {
	try {
		this.registerDiscoverEvents();
		this.discoverTokens();
	} catch (e) {}
}

/**
 *
 * @param {boolean} editorCacheReloaded
 *
 * Initalises discoverworker and connects it with editor cache worker
 */
function initializeDiscoverWorker(editorCacheReloaded) {
	this.discoverWorker = discoverInit.call(this);
	connectDiscoverWorkerWithEditorCacheWorker.call(this, editorCacheReloaded);
}

/**
 *
 * @param {number} startLineNumber first modified line number on an editor on text change event
 *
 * This method takes the starting line number and tokenize the text form startLineNumber to end of file and invokes worker to discover tokens on the lines
 */
function discoverEditorTokens(startLineNumber) {
	try {
		const lines = this.getEditorTokens(startLineNumber); //discoverWorker expects the tokens in an array of arrays (Editor dependent functionality)
		if (lines.length > 0) {
			this.discoverWorker.discoverTokens(
				this.apisDocResourceIds,
				lines,
				startLineNumber,
				{ currentScope: this.options.scope },
				this.updateDiscoveredTokensOnEditor.bind(this) // Once after getting tokens we update it to the editor (editor functionality)
			);
		}
	} catch (e) {}
}

/**
 *
 * @param {{}} element DOM element
 *
 * Checks the element is discovered or not and if it is a servicenow token it returns the meta data of the element {type,key,name}
 */
function getDiscoveredLiteral(element) {
	const elementClassList = element.classList;
	if (elementClassList.contains('now-code-editor-discoverable-text')) {
		let type;
		let key;
		const name = element.innerText.replace(/'|"/g, '');
		const listLength = elementClassList.length;
		for (let i = 0; i < listLength; i++) {
			const className = elementClassList.item(i);
			if (className.startsWith('scriptInclude')) {
				type = SYS_SCRIPT_INCLUDE;
				key = className.substring(className.indexOf('-') + 1);
			}
			if (className.startsWith('dbObject')) {
				type = SYS_DB_OBJECT;
				key = className.substring(className.indexOf('-') + 1);
			}
			if (className.startsWith('glideApi')) {
				type = API;
				key = className.substring(className.indexOf('-') + 1);
			}
		}
		if (!!key && !!type && !!name) {
			return { type, key, name };
		}
	}
	return;
}

export { initializeDiscoverWorker, discoverEditorTokens, getDiscoveredLiteral };
