import { actionTypes } from '@servicenow/ui-core';
import { JAVASCRIPT } from '../../constants/supportedLanguages';
import { NOW_CODE_EDITOR_CONTAINER_CLASS } from '../../constants/cssClasses';
import * as utils from '../graphQLEffects/helpers/utils';
import * as actions from '../../actions';
import getEditor from '../../editors/EditorFactory';
import initializeKeyBindings from './helpers/initializeKeyBindings';
import updateProperty from './helpers/updateProperty';

const lifeCycleHandlers = {
	[actionTypes.COMPONENT_DOM_READY]: {
		effect({ dispatch, properties, host, updateState }) {
			/**
			 * Get the element (dom node) on which the editor need to be mounted
			 * and create the instance of the editor.
			 */
			const element = host.shadowRoot.querySelector(`.${NOW_CODE_EDITOR_CONTAINER_CLASS}`);
			properties.height && (host.style.height = properties.height);
			properties.width && (host.style.width = properties.width);
			const editor = getEditor(element, properties);
			Object.preventExtensions(editor);
			updateState({ editor, initialHeight: element.clientHeight, initialWidth: element.clientWidth });
			initializeKeyBindings(editor, dispatch);
			dispatch(actions.REGISTER_ON_FOCUS_CHANGED);
			dispatch(actions.REGISTER_ON_CONTENT_CHANGED);
			editor.updateEditorMarkers(properties.markers);
		}
	},
	[actionTypes.COMPONENT_CONNECTED]: {
		effect({ properties, dispatch }) {
			if (properties.language === JAVASCRIPT) {
				utils.dispatchGraphQLRequests(properties, dispatch);
			}
		}
	},
	[actionTypes.COMPONENT_RENDERED]: {
		effect({ state: { editor } }) {
			editor.adjustEditorLayout();
		}
	},
	[actionTypes.COMPONENT_PROPERTY_CHANGED]: {
		effect(coeffects) {
			updateProperty(coeffects);
		}
	},
	[actions.SET_MACROS]: {
		effect({
			action: {
				payload: { macros }
			},
			state: { editor }
		}) {
			editor.setMacro(macros);
		},
		stopPropagation: true
	},
	[actions.REGISTER_ON_CONTENT_CHANGED]: {
		effect({ state: { editor }, dispatch, properties: { readOnly } }) {
			if (!readOnly) {
				editor.onEditorContentChanged((text, errors, lintingEnabled) => {
					dispatch(actions.NOW_CODE_EDITOR_CONTENT_CHANGED, { text, errors, lintingEnabled });
				});
			}
		},
		stopPropagation: true
	},
	[actions.REGISTER_ON_FOCUS_CHANGED]: {
		effect({ state: { editor }, dispatch }) {
			editor.onEditorFocusChanged(hasFocus => {
				dispatch(actions.NOW_CODE_EDITOR_TEXT_EDITOR_FOCUS_CHANGED, { hasFocus: hasFocus });
			});
		},
		stopPropagation: true
	}
};

export default lifeCycleHandlers;
