const userPreferenceFieldsFragment = `fragment preferenceFields on GlideDomain_Preference {
      name
      value
    }`;

const debugPointsQuery = `query($recordType : String!,
      $scriptField : String!, 
      $sysId : String!) {
      GlideDebugpoint_Query {
          debugPoints: getDebugpoints(scriptType: $recordType, scriptId:$sysId, scriptField: $scriptField){
              scriptId
              logpoints
              breakpoints
              logpointRole
              scriptDebuggerRole
          }
      }
      GlideNowCodeEditor_Query {
          logPointSystemPreference: getSystemProperty(systemProperty: "glide.debug.log_point") {
              value
              name
          }
      }
  }`;

const macrosQuery = `query {
    GlideNowCodeEditor_Query {
        macrosMetaData : getEditorResources {
            macros{
                text
                comments
                name
            }
        }
    }        
  }`;

const autoCompletePreferencesQuery = `query {
    GlideNowCodeEditor_Query {
        autoCompleteSystemPreference : getSystemProperty(systemProperty: "glide.ui.syntax_editor.autocomplete") {
            name
            value
        }
    }
    GlideDomain_Query {
        user{
          autoCompleteUserPreference: preferences(name: "glide.ui.syntax_editor.autocomplete") {
            ...preferenceFields
          }
        }
      }
  }
  ${userPreferenceFieldsFragment}`;

const lintConfigQuery = `query {
    GlideNowCodeEditor_Query {
        lintingSystemPreference : getSystemProperty(systemProperty: "glide.ui.syntax_editor.linter") {
            name
            value
        }
        esLintConfig : getSystemProperty(systemProperty: "glide.ui.syntax_editor.linter.eslint_config") {
            name
            value
        }
    }
    GlideDomain_Query {
        user{
            lintingUserPreference:preferences(name:"glide.ui.syntax_editor.linter") {
                ...preferenceFields
            }
        }
    }
  }
  ${userPreferenceFieldsFragment}`;

const apiDocResourcesQuery = `query($recordType : String!,
    $scriptField : String!) {
        GlideNowCodeEditor_Query {
            contextMenuSystemPreference : getSystemProperty(systemProperty: "glide.ui.syntax_editor.context_menu") {
                name
                value
            }
            editorResources: getEditorResources(table: $recordType, column: $scriptField) {
                apisDocResourceIds
            }
        }    
  }`;

const flushTimesQuery = `query{
    GlideNowCodeEditor_Query {
        flushTimes : getEditorResources {
            sysDbObjectFlushTime
            sysScriptIncludeFlushTime
        }
    }      
  }`;

export {
	debugPointsQuery,
	macrosQuery,
	autoCompletePreferencesQuery,
	apiDocResourcesQuery,
	lintConfigQuery,
	flushTimesQuery
};
