import * as actions from '../../actions';
import toggleSyntaxHighlighting from './helpers/toggleSyntaxHighlighting';
import openScriptDebugger from './helpers/openScriptDebugger';

const toolbarHandlers = {
	[actions.TOGGLED_COMMENT]: {
		effect({ state: { editor } }) {
			editor.toggleComment();
		},
		stopPropagation: true
	},
	[actions.TOGGLED_SYNTAX_CHECK]: {
		effect({ state: { editor, syntaxCheckSelected }, updateState }) {
			editor.toggleSyntaxCheck();
			updateState({ syntaxCheckSelected: !syntaxCheckSelected });
		},
		stopPropagation: true
	},
	[actions.FORMATED_CODE]: {
		effect({ state: { editor } }) {
			editor.formatCode();
		},
		stopPropagation: true
	},
	[actions.SEARCHED]: {
		effect({ state: { editor } }) {
			editor.findAction();
		},
		stopPropagation: true
	},
	[actions.TOGGLED_FULLSCREEN]: {
		effect({
			state: { editor, fullScreenSelected },
			properties: { disableFullScreen },
			updateState,
			host,
			dispatch
		}) {
			if (!disableFullScreen) {
				editor.toggleFullscreen(host);
				dispatch(actions.NOW_CODE_EDITOR_FULLSCREEN_TOGGLED, { isFullScreen: !fullScreenSelected });
				updateState({ fullScreenSelected: !fullScreenSelected });
			}
		},
		stopPropagation: true
	},
	[actions.TOGGLE_WORD_WRAP]: {
		effect({ state: { editor, wordWrapSelected }, updateState }) {
			editor.toggleWordWrap();
			updateState({ wordWrapSelected: !wordWrapSelected });
		},
		stopPropagation: true
	},
	[actions.TOGGLE_MINIMAP]: {
		effect({ state: { editor, minimapSelected }, updateState }) {
			editor.toggleMinimap();
			updateState({ minimapSelected: !minimapSelected });
		},
		stopPropagation: true
	},
	[actions.SHOWN_KEYMAP]: {
		effect({ state: { isOpenKeymap }, updateState }) {
			updateState({ isOpenKeymap: !isOpenKeymap });
		},
		stopPropagation: true
	},
	[actions.NOW_MODAL_OPENED_SET]: {
		effect({ dispatch }) {
			dispatch(actions.SHOWN_KEYMAP);
		},
		stopPropagation: true
	},
	[actions.NOW_MODAL_FOOTER_ACTION_CLICKED]: {
		effect({ dispatch }) {
			dispatch(actions.SHOWN_KEYMAP);
		},
		stopPropagation: true
	},
	[actions.TOGGLE_SYNTAX_HIGHLIGHTING]: {
		effect({
			properties: { disableSyntaxHighlighting, language },
			state: { editor, syntaxHighlightingSelected },
			updateState
		}) {
			if (!disableSyntaxHighlighting) {
				toggleSyntaxHighlighting(editor, language);
				updateState({ syntaxHighlightingSelected: !syntaxHighlightingSelected });
			}
		},
		stopPropagation: true
	},
	[actions.CLOSED_MORE_OPTIONS]: {
		effect: ({ state: { moreOptionSelected }, updateState }) => {
			updateState({ moreOptionSelected: !moreOptionSelected });
		},
		stopPropagation: true
	},
	[actions.NOW_POPOVER_OPENED_SET]: {
		effect: ({ state: { moreOptionSelected }, updateState }) => {
			updateState({ moreOptionSelected: !moreOptionSelected });
		},
		stopPropagation: true
	},
	[actions.OPEN_SCRIPT_DEBUGGER]: {
		effect: ({ properties: { sysId, recordType, scriptField } }) => {
			openScriptDebugger(sysId, recordType, scriptField);
		},
		stopPropagation: true
	}
};

export default toolbarHandlers;
