import { getGutterContextMenuItems, gutterContextMenuHandle } from './gutterContextMenu';
import { GUTTER_CONTEXT_MENU } from '../../../constants/contextMenu';

/**
 *
 * @param {{}} coeffects Seismic event object
 *
 * UpdateState for contextMenuItems, openedContextMenu and isContextMenuPositionedAbove
 */
function renderContextMenu(coeffects) {
	const {
		action: {
			payload: { position, contextMenuType }
		},
		state: { contextMenuButtonElement },
		updateState
	} = coeffects;

	const contextMenuItems = getContextMenuItems(coeffects);
	const isContextMenuPositionedAbove = positionContextMenu(
		contextMenuButtonElement,
		position,
		contextMenuItems ? contextMenuItems.length : 0
	);
	if (contextMenuItems && contextMenuItems.length !== 0) {
		updateState({
			contextMenuItems,
			openedContextMenu: { type: contextMenuType, isOpen: true },
			isContextMenuPositionedAbove: isContextMenuPositionedAbove
		});
	}
}

//position Context menu based on the position of the mouse event
function positionContextMenu(contextMenuButtonElement, position, contextMenuItemsLength) {
	contextMenuButtonElement.style.left = position.x + 'px';
	contextMenuButtonElement.style.top = position.y + 'px';
	if (window.innerHeight - position.y < contextMenuItemsLength * 33) {
		return true;
	} else {
		return false;
	}
}

/**
 *
 * @param {{}} coeffects Seismic event object
 *
 * Returns items for contextmenu
 */
function getContextMenuItems({ action: { payload }, state }) {
	const { contextMenuType } = payload;
	if (contextMenuType === GUTTER_CONTEXT_MENU) {
		const { lineNumber, logPointRole, scriptDebuggerRole, logPointSystemPreference } = payload;
		const { breakPoints, logPoints } = state;
		return getGutterContextMenuItems(
			lineNumber,
			breakPoints[lineNumber],
			logPoints[lineNumber],
			logPointRole,
			scriptDebuggerRole,
			logPointSystemPreference
		);
	}
}

/**
 *
 * @param {{}} coeffects Seismic event object
 *
 * contextMenuItemClick Updates State of openedContextMenu
 */
function contextMenuItemClick(coeffects) {
	const {
		state: {
			openedContextMenu: { type }
		},
		updateState
	} = coeffects;

	if (type === GUTTER_CONTEXT_MENU) {
		gutterContextMenuHandle(coeffects);
	}
	updateState({ openedContextMenu: { type: '', isOpen: false } });
}

export { renderContextMenu, contextMenuItemClick };
