import monaco from './NowMonaco';
import AbstractEditor from '../AbstractEditor';
import { JAVASCRIPT, TYPESCRIPT, HTML, JSON, XML, CSS, PLAIN_TEXT } from '../../constants/supportedLanguages';
import getTheme from './features/themes';
import font from 'monaco-editor/min/vs/base/browser/ui/codiconLabel/codicon/codicon.ttf';
import setMacros from './features/setMacros';
import { getEditorTokens, updateTokensOnEditor, registerDiscoverEvents } from './features/discoverability';
import enableAutoComplete from './features/codeCompletion/autocompleteSuggestions';
import { renderBreakPoints, renderLogPoints } from '../monaco/features/renderDebugPoints';
import DebugService from './services/debugPointService';
import beautifyCode from './features/beautifyCode';
import gutterClickAction from './features/gutterClickMouseAction';
import { lintValidator } from '../editorFeatures/linting';
import updateMarkers from './features/updateMarker';
import { LINT_MARKER, CUSTOM_MARKER } from '../../constants/markerOwners';
import {
	NOW_CODE_EDITOR_FULLSCREEN_CLASS,
	NOW_CODE_EDITOR_WITHOUT_GLYPH_MARGIN,
	NOW_CODE_EDITOR_WITH_GLYPH_MARGIN
} from '../../constants/cssClasses';
import * as messageSeverities from '../../constants/messageSeverities';
import _ from 'lodash';

self.MonacoEnvironment = {
	getWorker: function(moduleId, label) {
		if (label === TYPESCRIPT || label === JAVASCRIPT) {
			return new Worker('monaco-editor/esm/vs/language/typescript/ts.worker.js');
		} else if (label === JSON) {
			return new Worker('monaco-editor/esm/vs/language/json/json.worker.js');
		} else if (label === HTML) {
			return new Worker('monaco-editor/esm/vs/language/html/html.worker.js');
		} else if (label === CSS) {
			return new Worker('monaco-editor/esm/vs/language/css/css.worker.js');
		}
		return new Worker('monaco-editor/esm/vs/editor/editor.worker.js');
	}
};
export default class MonacoEditor extends AbstractEditor {
	_gutterDecorations = [];
	_showMinimap = true;
	_enableWordWrap = false;
	_gutterActions = {
		onMouseDownEvent: undefined,
		onContextMenuEvent: undefined,
		onDidScrollChangeEvent: undefined,
		onMouseLeaveEvent: undefined,
		onMouseMoveEvent: undefined,
		onKeyDownEvent: undefined,
		onDidChangeContentEvent: undefined
	};
	_glyphHoverOverlayWidget = undefined;

	constructor(element, options) {
		super(element, options);
		this._discoverDecorations = [];
		this._initMonaco();
		return this;
	}

	get discoverDecorations() {
		return this._discoverDecorations;
	}

	set discoverDecorations(discoverDecorations) {
		this._discoverDecorations = discoverDecorations;
	}

	_initMonaco() {
		this._initFonts();

		this._setMonacoOptions();

		const editorWithGlyphMargin =
			this.options.language === JAVASCRIPT && !this.options.disableDebugging && !this.options.isClientScript;
		const editor = monaco.editor.create(this.element, {
			value: this.options.content,
			language: this.options.disableSyntaxHighlighting ? PLAIN_TEXT : this.options.language,
			minimap: { enabled: !this.options.disableMinimap },
			readOnly: this.options.readOnly || false,
			theme: getTheme(this.options.theme),
			glyphMargin: this.options.hideGutter ? false : editorWithGlyphMargin,
			ariaLabel: this.options.ariaLabel,
			lineNumbersMinChars: this.options.hideGutter ? 0 : 3,
			lineNumbers: this.options.hideGutter ? 'off' : 'on',
			folding: !this.options.hideGutter,
			lineDecorationsWidth: this.options.hideGutter ? 0 : editorWithGlyphMargin ? '6px' : '8px',
			wrappingIndent: 'same',
			contextmenu: false,
			extraEditorClassName: this.options.hideGutter
				? ''
				: editorWithGlyphMargin
				? NOW_CODE_EDITOR_WITH_GLYPH_MARGIN
				: NOW_CODE_EDITOR_WITHOUT_GLYPH_MARGIN
		});

		this._initMonacoContainer();
		this.instance = editor;
		this.setCursorPosition(this.options.cursorPosition);
	}

	_initFonts() {
		const codiconCSS = `@font-face { font-family: "codicon"; src: url(${font}) format("truetype"); }`;
		const style = document.createElement('style');
		style.type = 'text/css';
		style.media = 'screen';
		style.setAttribute('skipCheck', true);
		document.getElementsByTagName('head')[0].appendChild(style);
		style.innerHTML = codiconCSS;
	}

	/**
	 * This function is single place to override/set monaco library option
	 */
	_setMonacoOptions() {
		if (this.options.language === JAVASCRIPT) {
			// set noSuggestionDiagnostics to true for javaScript
			const currentDiagnosticsOptions = monaco.languages.typescript.javascriptDefaults.getDiagnosticsOptions();
			monaco.languages.typescript.javascriptDefaults.setDiagnosticsOptions({
				...currentDiagnosticsOptions,
				noSyntaxValidation: true,
				noSuggestionDiagnostics: true
			});
			// setCompilerOptions allows extensions to add custom Lib and also hide global window suggestions
			const currentCompilerOptions = monaco.languages.typescript.javascriptDefaults.getCompilerOptions();
			monaco.languages.typescript.javascriptDefaults.setCompilerOptions({
				...currentCompilerOptions,
				noLib: true
			});
		}
	}

	_initMonacoContainer() {
		/**
		 * This is special case we are handling :
		 *	1.	Monaco-editor adds a dev element to the documemt.body for accessibility purpose.
		 *	2.	As we are rendering the Monaco-editor inside shodow DOM, the bundled CSS will not be available at document.body level, so we are adding inline CSS for this element.
		 */
		const monacoAriaContainer = document.getElementsByClassName('monaco-aria-container')[0] || {};
		monacoAriaContainer.style = monacoAriaContainer.style || {};
		if (monacoAriaContainer.style.position !== 'absolute' && monacoAriaContainer.style.left !== '-999em') {
			monacoAriaContainer.style.position = 'absolute';
			monacoAriaContainer.style.left = '-999em';
		}
	}

	adjustEditorLayout() {
		this.instance.layout();
	}

	setCursorPosition(postion) {
		this.instance.setPosition(postion);
	}

	markLintErrors({ errors, version }) {
		const model = this.instance.getModel();
		if (version !== model.getVersionId()) {
			return;
		}
		updateMarkers(model, LINT_MARKER, errors);
	}

	removeLintErrors() {
		const model = this.instance.getModel();
		updateMarkers(model, LINT_MARKER, []);
		if (this.dispatchContentChanged) {
			this.dispatchContentChanged({ errors: [], version: model.getVersionId() });
		}
	}

	getEditorLintData() {
		const model = this.instance.getModel();
		return {
			content: model.getValue(),
			version: model.getVersionId()
		};
	}

	/**
	 * Initalize DebugPointsService to perform actions related to Debugpoints
	 */
	debugPointService() {
		this.debugPointInstance = this.debugPointInstance
			? this.debugPointInstance
			: new DebugService(this.instance, this._gutterDecorations);
		return this.debugPointInstance;
	}

	/**
	 *
	 * @param {{}} debugPoints
	 * calls method to render breakpoints and logpoints decoration at the time of initilalization
	 */
	renderDebugPoints(debugPoints) {
		const { breakPoints, logPoints } = debugPoints;
		const debugService = this.debugPointService();

		debugService.updateBreakPoints(breakPoints);
		debugService.updateLogPoints(logPoints);

		debugService.removeAllDebugPointsDecorations();

		renderLogPoints.call(this);
		renderBreakPoints.call(this);
	}

	/**
	 *
	 * @param {function} gutterClickCommand a callback function
	 * registering the F9 key to open the context menu for debugPoints and register other events on gutter related to debugpoints
	 */
	registerGutterClick(gutterClickCommand) {
		this.instance.addCommand(monaco.KeyCode.F9, () => {
			const editorPosition = this.element.parentElement.getBoundingClientRect();
			const currentLineNumber = this.instance.getPosition().lineNumber;
			const topForLineNumber = this.instance.getTopForLineNumber(currentLineNumber);
			const editorScrollTop = this.instance.getScrollTop();
			const x = editorPosition.left + 8;
			const y = Math.round(editorPosition.y + this.element.offsetTop + topForLineNumber - editorScrollTop) + 12;
			gutterClickCommand(currentLineNumber, { x, y });
		});
		gutterClickAction.call(this, gutterClickCommand);
	}

	/**
	 * unbind the shortcut key for debugpoints and dispose mouse events on gutter
	 */
	unBindDebugging() {
		const debugService = this.debugPointService();
		debugService.removeAllDebugPointsDecorations();
		this.instance.addCommand(monaco.KeyCode.F9, () => {});
		gutterClickAction.call(this);
	}

	updateEditorOptions(option, newValue) {
		if (option === 'content') {
			this.instance.getModel().setValue(newValue);
		} else if (option === 'theme') {
			this.instance.updateOptions({ theme: getTheme(newValue) });
		} else {
			this.instance.updateOptions({ [option]: newValue });
		}
	}

	/**
	 * update monaco's gutter according to the properties changed
	 */
	updateGutterOptions() {
		const editorWithGlyphMargin =
			this.options.language === JAVASCRIPT && !this.options.disableDebugging && !this.options.isClientScript;

		this.instance.updateOptions({
			glyphMargin: this.options.hideGutter ? false : editorWithGlyphMargin,
			lineNumbersMinChars: this.options.hideGutter ? 0 : 3,
			lineNumbers: this.options.hideGutter ? 'off' : 'on',
			folding: !this.options.hideGutter,
			lineDecorationsWidth: this.options.hideGutter ? 0 : editorWithGlyphMargin ? '6px' : '8px',
			extraEditorClassName: this.options.hideGutter
				? ''
				: editorWithGlyphMargin
				? NOW_CODE_EDITOR_WITH_GLYPH_MARGIN
				: NOW_CODE_EDITOR_WITHOUT_GLYPH_MARGIN
		});
	}

	formatCode() {
		const language = this.options.language;
		if (language == JAVASCRIPT || language == XML || language == CSS) {
			beautifyCode(this.instance, language);
			return;
		}

		if (this.instance.getSelection().isEmpty()) {
			this.instance.getAction('editor.action.formatDocument').run();
		} else {
			this.instance.getAction('editor.action.formatSelection').run();
		}
	}

	toggleComment() {
		this.instance.getAction('editor.action.commentLine').run();
	}

	findAction() {
		this.instance.getAction('actions.find').run();
	}

	toggleFullscreen(host) {
		this.isFullScreen = !this.isFullScreen;
		if (this.isFullScreen) {
			host.classList.add(NOW_CODE_EDITOR_FULLSCREEN_CLASS);
		} else {
			host.classList.remove(NOW_CODE_EDITOR_FULLSCREEN_CLASS);
		}
		this.adjustEditorLayout();
	}

	toggleMinimap() {
		this._showMinimap = !this._showMinimap;
		this.instance.updateOptions({
			minimap: {
				enabled: this._showMinimap
			}
		});
	}

	enableMinimap(enable) {
		this.instance.updateOptions({
			minimap: {
				enabled: enable
			}
		});
	}

	toggleWordWrap() {
		this._enableWordWrap = !this._enableWordWrap;
		this.instance.updateOptions({
			wordWrap: this._enableWordWrap
		});
	}

	setLanguage(language) {
		this.options.language = language;
		monaco.editor.setModelLanguage(this.instance.getModel(), language);

		if (language === JAVASCRIPT) {
			this._setMonacoOptions();
			this.discoverTokens();
		} else {
			this.updateDiscoveredTokensOnEditor();
		}
	}

	addHelpCommand(command) {
		this.instance.addCommand(monaco.KeyMod.CtrlCmd | monaco.KeyCode.KEY_H, command);
	}

	addFullscreenCommand(command) {
		this.instance.addCommand(monaco.KeyMod.WinCtrl | monaco.KeyCode.KEY_M, command);
	}

	addSyntaxCheckCommand(command) {
		this.instance.addCommand(monaco.KeyMod.Shift | monaco.KeyMod.Alt | monaco.KeyCode.KEY_C, command);
	}

	addFormatCodeCommand(command) {
		this.instance.addCommand(monaco.KeyMod.Shift | monaco.KeyMod.Alt | monaco.KeyCode.KEY_F, command);
	}

	setMacro(macrosList) {
		setMacros(macrosList);
	}

	registerDiscoverEvents() {
		registerDiscoverEvents.call(this);
	}

	getEditorTokens(startLineNumber) {
		return getEditorTokens.call(this, startLineNumber);
	}

	updateDiscoveredTokensOnEditor(result = {}) {
		updateTokensOnEditor.call(this, result);
	}

	enableAutoComplete(apisDocResourceFlushTime, isEnabled) {
		this.isAutocompleteEnabled = isEnabled;
		enableAutoComplete.call(this, apisDocResourceFlushTime);
	}

	onEditorFocusChanged(callback) {
		this.instance.onDidFocusEditorText(() => {
			callback(true);
		});
		this.instance.onDidBlurEditorText(() => {
			callback(false);
		});
	}

	onEditorContentChanged(callback) {
		if (!this.onContentChangedCallback) {
			this.onContentChangedCallback = typeof callback === 'function' ? callback : null;
			const model = this.instance.getModel();

			const severities = {
				[monaco.MarkerSeverity.Error]: messageSeverities.ERROR,
				[monaco.MarkerSeverity.Warning]: messageSeverities.WARNING,
				[monaco.MarkerSeverity.Hint]: messageSeverities.HINT,
				[monaco.MarkerSeverity.Info]: messageSeverities.INFO
			};

			//Debouncing the eslint validator to 500ms on keypress before calling eslint worker
			const javascriptLintValidatorDebounce = _.debounce(() => {
				lintValidator.call(this);
			}, 500);

			//For other language lint issues Monaco validators are being used, Moanco internally debouncing the validator feature for 500ms and exposing an api to access those errors
			//So the validateContent is being called after a debouncing of 600ms to make sure getModelMarkers api will give the latest lint errors
			const validateContent = () => {
				const version = model.getVersionId();
				let errors = [];
				if (this.options.language !== JAVASCRIPT) {
					const markers = monaco.editor.getModelMarkers({
						owner: this.options.language,
						resource: model.uri
					});
					errors = markers.map(marker => ({
						severity: severities[marker.severity],
						message: marker.message,
						from: { line: marker.startLineNumber, column: marker.startColumn },
						to: { line: marker.endLineNumber, column: marker.endColumn }
					}));
				}
				this.dispatchContentChanged({ errors, version });
			};

			const lintValidatorDebounce = _.debounce(validateContent, 600);

			//Registering an onContentChange event on monaco to fetch the latest lint errors and dispatch it to its parent
			//Using custom eslint to validate js and using monaco validators for other languages
			model.onDidChangeContent(() => {
				if (this.enableSyntaxCheck && this.options.language === JAVASCRIPT) {
					javascriptLintValidatorDebounce();
				} else {
					lintValidatorDebounce();
				}
			});

			//Dispatch the lint errors on page load for the first time, eslint is validating and dispatching the errors to parent on init
			//For all other cases, errors are fetched from monaco api and dispatch to parent after 600ms
			if (this.options.language !== JAVASCRIPT) {
				setTimeout(validateContent, 600);
			}
		}
	}

	dispatchContentChanged({ errors, version }) {
		if (this.onContentChangedCallback) {
			const model = this.instance.getModel();
			if (version !== model.getVersionId()) {
				return;
			}
			const editorContent = model.getValue();
			this.onContentChangedCallback(
				editorContent,
				errors,
				this.options.language === JAVASCRIPT ? !!this.enableSyntaxCheck : true
			);
		}
	}

	updateEditorMarkers(markers) {
		const model = this.instance.getModel();
		updateMarkers(model, CUSTOM_MARKER, markers);
	}
}
