import monaco from '../NowMonaco';
import { JAVASCRIPT } from '../../../constants/supportedLanguages';

let areMacrosSetToMonaco = false;

function setMacros(macrosList) {
	if (!areMacrosSetToMonaco) {
		macrosList = macrosList.map(({ name: label, text: insertText, comments }) => ({
			label,
			documentation: comments || '',
			insertText,
			kind: monaco.languages.CompletionItemKind.Snippet,
			insertTextRules: monaco.languages.CompletionItemInsertTextRule.InsertAsSnippet
		}));

		monaco.languages.registerCompletionItemProvider(JAVASCRIPT, {
			provideCompletionItems: () => {
				return { suggestions: macrosList };
			}
		});
		areMacrosSetToMonaco = true;
	}
}

export default setMacros;
