import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles/index.scss';
import view from './view';
import actionHandlers from './actionHandlers';
import properties from './propertiesConfiguration';
import initialState from './initialState';
import resizeBehavior from '@servicenow/behavior-resize';

/**
 * Now Code Editor provides a the coding capablities for the platform
 * this editor could be used in conjuction with script and htmls
 *
 * ## Header here
 * TODO: properties are to be documented
 *
 * @summary A Resuable Editor component
 * @seismicElement now-code-editor
 */
createCustomElement('now-code-editor', {
	renderer: { type: snabbdom },
	view,
	properties,
	actionHandlers,
	styles,
	initialState,
	behaviors: [{ behavior: resizeBehavior }]
});
