import { ESLINT_BUNDLE_PATH } from '../../constants/paths';

// The function will generate a unique hashCode for the given string
const generateHashCode = function(url) {
	const lengh = url.length;
	let hashCode = 0;
	let index = 0;

	if (lengh > 0) {
		while (index < lengh) {
			hashCode = ((hashCode << 5) - hashCode + url.charCodeAt(index++)) | 0;
		}
	}
	return hashCode;
};
function initLintWorker(linterUrl) {
	if (typeof Worker === undefined) {
		// TODO : logging to inform user
		return false;
	}
	if (!linterUrl || linterUrl.lengh === 0) {
		linterUrl = ESLINT_BUNDLE_PATH;
	}
	const workerName = 'linter_' + generateHashCode(linterUrl);

	if (!window.SharedWorker) {
		this.lintWorker._workerInstance = new Worker('../../../workers/lintWorker');
	} else {
		const lintSharedWorker = new SharedWorker('../../../workers/lintWorker', workerName);
		this.lintWorker._workerInstance = lintSharedWorker.port;
	}

	// Sending the message to initize the linter
	this.lintWorker._workerInstance.postMessage({ linterUrl });

	this.lintWorker.onmessage(({ data }) => {
		this.markLintErrors(data);
		this.dispatchContentChanged(data);
	});
	return true;
}

function lintValidator() {
	const data = this.getEditorLintData();
	data.eslintOptions = this.eslintConfig;
	this.lintWorker.postMessage(data);
}

export { initLintWorker, lintValidator };
