import MonacoEditor from './monaco/MonacoEditor';

/* TODO : In future if we support more than one type of editor, then we might need to include the editor dynamically.
*	[Currently not doing as tectonic doesnt support dynamic import].
*/

/**
 *
 * @param {string} element DOM Node on which editor will be mounted
 * @param {{}} options options to be passed to the editor
 */
function getEditor(element, options) {
	return new MonacoEditor(element, options);
}


export default getEditor;
