import * as actions from '../../actions';
import { renderContextMenu, contextMenuItemClick } from './helpers/contextMenuHandler';

const contextMenuHandlers = {
	[actions.RENDER_CONTEXT_MENU]: {
		effect(coeffects) {
			renderContextMenu(coeffects);
		},
		stopPropagation: true
	},
	[actions.NOW_DROPDOWN_PANEL_ITEM_CLICKED]: {
		effect(coeffects) {
			contextMenuItemClick(coeffects);
		},
		stopPropagation: true
	}
};

export default contextMenuHandlers;
