import { t } from 'sn-translate';
import cuid from 'cuid';

const getKeyboardShortcutsView = keyBoardShortcuts => {
	return (
		<div className="now-code-editor-keyboard-shortcuts" key={cuid()}>
			<table
				className="now-code-editor-keyboard-shortcuts-table"
				aria-label={t('To know more actions and their shortcut keys, use up and down arrows')}
			>
				<thead>
					<tr>
						<th></th>
						<th>{t('Action')}</th>
						<th>{t('Shortcut keys')}</th>
					</tr>
				</thead>
				<tbody>
					{keyBoardShortcuts.map((shortcut, index) => {
						const tabIndex = index == 0 ? 0 : -1;
						return (
							<tr>
								<td>{shortcut.icon && <now-icon icon={shortcut.icon} size="md"></now-icon>}</td>
								<td
									tabIndex={tabIndex}
									id={index}
									className={index == 0 ? 'first-shortcut-action' : 'shortcut-action'}
								>
									<div aria-label={shortcut.ariaLabel}> {shortcut.action}</div>
								</td>
								<td>{shortcut.command}</td>
							</tr>
						);
					})}
				</tbody>
			</table>
		</div>
	);
};

const view = state => {
	const {
		properties: { isOpenKeymap, keyBoardShortcuts }
	} = state;

	return (
		<now-modal
			opened={isOpenKeymap}
			size="lg"
			header-label={t('Keyboard shortcuts')}
			footerActions={[{ label: t('Close'), tooltipContent: t('Close dialog'), variant: 'secondary' }]}
			manage-opened
		>
			{getKeyboardShortcutsView(keyBoardShortcuts)}
		</now-modal>
	);
};

export default view;
