import generateMoreDropDown from './generateMoreDropDownConfig';
import generateSections from './generateSectionsConfig';
import getKeyBoardShortcuts from './getKeyBoardShortcuts';
import updateToolbarData from './updateToolbarData';

const getToolbarData = state => {
	const {
		isLintingEnabled,
		initialWidth,
		behaviors: {
			resize: { size }
		},
		syntaxCheckSelected,
		syntaxHighlightingSelected,
		minimapSelected,
		wordWrapSelected,
		fullScreenSelected,
		moreOptionSelected,
		properties: {
			readOnly,
			isClientScript,
			language,
			disableSyntaxHighlighting,
			linting: { disabled: disableLintingComponentPreference },
			disableDebugging: disableDebuggingComponentPreference,
			hideGutter: hideGutterComponentPreference,
			disableFullScreen: disableFullScreenComponentPreference,
			disableMinimap: disableMinimapComponentPreference
		}
	} = state;
	const availableWidth = size.width === null ? initialWidth : size.width;

	const keyBoardShortcuts = getKeyBoardShortcuts({
		language,
		disableLintingComponentPreference,
		disableFullScreenComponentPreference,
		disableDebuggingComponentPreference,
		hideGutterComponentPreference,
		isClientScript
	});

	const moreDropDown = generateMoreDropDown({
		disableSyntaxHighlighting,
		disableMinimapComponentPreference,
		syntaxHighlightingSelected,
		minimapSelected,
		wordWrapSelected
	});

	const toolBarSections = generateSections(
		isLintingEnabled,
		readOnly,
		isClientScript,
		disableLintingComponentPreference,
		disableDebuggingComponentPreference,
		disableFullScreenComponentPreference,
		language,
		syntaxCheckSelected,
		fullScreenSelected,
		moreOptionSelected
	);

	updateToolbarData(toolBarSections, moreDropDown, availableWidth - 2);

	return { toolBarSections, moreDropDown, keyBoardShortcuts };
};
export default getToolbarData;
