import * as actions from './../../../actions';
import { GUTTER_CONTEXT_MENU } from '../../../constants/contextMenu';

function handleGutterClick({
	action: {
		payload: { lineNumber, position, logPointRole, scriptDebuggerRole, logPointSystemPreference }
	},
	state: { breakPoints, logPoints, openedContextMenu },
	properties: { sysId, recordType, scriptField, readOnly },
	dispatch,
	updateState
}) {
	if (position === undefined && lineNumber !== undefined && scriptDebuggerRole) {
		const data = { evaluationString: '' };

		if (breakPoints[lineNumber]) {
			dispatch(actions.REMOVE_BREAKPOINT, { recordType, sysId, scriptField, lineNumber });
		} else if (logPoints[lineNumber]) {
			dispatch(actions.REMOVE_LOGPOINT, { recordType, sysId, scriptField, lineNumber });
		} else {
			dispatch(actions.ADD_BREAKPOINT, { recordType, sysId, scriptField, lineNumber, data });
		}
	} else if (position !== undefined && lineNumber !== undefined && !readOnly) {
		dispatch(actions.RENDER_CONTEXT_MENU, {
			lineNumber,
			position,
			contextMenuType: GUTTER_CONTEXT_MENU,
			logPointRole,
			scriptDebuggerRole,
			logPointSystemPreference
		});
	} else {
		if (openedContextMenu.isOpen) {
			updateState({ openedContextMenu: { type: '', isOpen: false } });
		}
	}
}

export default handleGutterClick;
