function initializeEditorCacheWorker() {
	if (!window.SharedWorker) {
		return;
	}
	const callbacks = {};
	let editorCacheWorker = new SharedWorker('../../../workers/editorCacheWorker');
	let editorCacheWorkerPort = editorCacheWorker.port;

	editorCacheWorkerPort.onmessage = event => {
		try {
			const data = event.data;
			const id = data.id || '';
			const operation = data.operation || '';
			if (callbacks[operation] && callbacks[operation][id]) {
				callbacks[operation][id].callback && callbacks[operation][id].callback(data || {});
			}
			removeCallback(data.operation, callbacks, data.id);
		} catch (e) {}
	};

	return {
		loadCache: (cacheFlushTime, callback) => {
			try {
				const operation = 'loadCache';
				const id = addCallback(operation, callbacks, callback);

				editorCacheWorkerPort.postMessage({
					operation,
					id,
					cacheFlushTime,
					g_ck: window.g_ck
				});
			} catch (e) {}
		},
		establishConnectionWithAutoCompleteWorker: portToCommunicateWithAutoCompleteWorker => {
			const operation = 'establishConnectionWithAutoCompleteWorker';
			editorCacheWorkerPort.postMessage(
				{
					operation,
					portToCommunicateWithAutoCompleteWorker
				},
				[portToCommunicateWithAutoCompleteWorker]
			);
		},
		establishConnectionWithDiscoverWorker: (data, callback) => {
			const { portToCommunicateWithDiscoverWorker, editorCacheReloaded } = data;
			const operation = 'establishConnectionWithDiscoverWorker';
			const id = addCallback(operation, callbacks, callback);
			editorCacheWorkerPort.postMessage(
				{
					operation,
					id,
					portToCommunicateWithDiscoverWorker,
					editorCacheReloaded
				},
				[portToCommunicateWithDiscoverWorker]
			);
		},
		destroy: () => {
			try {
				if (editorCacheWorkerPort) {
					editorCacheWorkerPort.close && editorCacheWorkerPort.close();
					editorCacheWorker = editorCacheWorkerPort = null;
				}
			} catch (e) {}
		}
	};
}

function addCallback(operation, callbacks, callback) {
	try {
		callbacks[operation] = callbacks[operation] || {};
		const id = Math.floor(Math.random() * 1001);
		callbacks[operation][id] = {
			callback: callback,
			start: Date.now()
		};

		return id;
	} catch (e) {}
}

function removeCallback(operation, callbacks, id) {
	try {
		if (callbacks[operation]) {
			delete callbacks[operation][id];
		}
	} catch (e) {}
}

export default initializeEditorCacheWorker;
