function launchScriptDebugger(sysId, recordType, scriptField) {
	const width = window.top.innerWidth - 40;
	const height = window.top.innerHeight;
	const x = window.top.screenX + 20;
	const y = window.top.screenY + 20;

	const features = `width=${width},height=${height},toolbar=no,status=no,directories=no,menubar=no,resizable=yes,screenX=${x},left=${x},screenY=${y},top=${y}`;

	let debuggerWind = window.open('', 'sn_ScriptDebugger', features, false);
	const prevFullUrl = debuggerWind.location.href;
	let reload = false;

	if (prevFullUrl === 'about:blank') {
		// Debugger doesn't exist or is opened by other window (can't get reference)
		try {
			const storedTime = localStorage.getItem('sn_ScriptDebugger');
			const currentTime = new Date().getTime();

			if (storedTime && currentTime - storedTime < 60000) {
				// Debugger exists - Most recent storedTime is within one Interval of setting localStorage
				debuggerWind.close();
				localStorage.setItem('sn_ScriptDebuggerExist_ShowNotification', new Date().getTime());
				return;
			}
		} catch (e) {}

		//debugger window doesn't exist
		reload = true;
	}
	const url = `$jsdebugger.do?scriptId=${sysId}&scriptType=${recordType}&scriptField=${scriptField}&sysparm_nostack=true`;

	if (!reload) {
		// debugger window is already open; check if needs to reload different script
		const prevUrl = prevFullUrl.slice(prevFullUrl.indexOf('$jsdebugger.do'));
		if (prevUrl != url) {
			reload = true;
		}
	}

	if (reload) {
		debuggerWind = window.open(url, 'sn_ScriptDebugger', features, false);
	}

	// bring it to the front
	debuggerWind.focus();
	debuggerWind.setTimeout(focus, 100);
}

function openScriptDebugger(sysId, recordType, scriptField) {
	let launchFunction;
	if (window.top.launchScriptDebugger) {
		launchFunction = window.top.launchScriptDebugger;
	} else if (window.top.opener && window.top.opener.top.launchScriptDebugger) {
		launchFunction = window.top.opener.top.launchScriptDebugger;
	} else {
		launchFunction = launchScriptDebugger;
	}
	launchFunction(sysId, recordType, scriptField);
}

export default openScriptDebugger;
