//TODO we need to take this const dynamically once NDS provide this
const widthRequiredbyControl = 40;
const paddingRequiredBySection = 8;

const calculateRequiredWidth = allSections => {
	const sectionsAndControlsReducer = (sectionsAndControlsAccumulator, currentSection) => {
		const currentSectionControls = currentSection.length;
		if (currentSectionControls > 0) {
			return {
				sections: sectionsAndControlsAccumulator.sections + 1,
				controls: sectionsAndControlsAccumulator.controls + currentSectionControls
			};
		} else {
			return sectionsAndControlsAccumulator;
		}
	};
	let sectionsAndControls = allSections.leftAligned.reduce(sectionsAndControlsReducer, { sections: 0, controls: 0 });
	sectionsAndControls = allSections.rightAligned.reduce(sectionsAndControlsReducer, sectionsAndControls);
	const { sections, controls } = sectionsAndControls;
	const requiredWidth = widthRequiredbyControl * controls + paddingRequiredBySection * sections;

	return requiredWidth;
};

function removeControlsInSection(section, availableWidth, requiredWidth, controlsPopped) {
	while (section.length > 0) {
		controlsPopped.push(section.pop());
		requiredWidth = requiredWidth - widthRequiredbyControl;
		if (availableWidth >= requiredWidth) {
			break;
		}
	}
	if (section.length === 0) {
		requiredWidth = requiredWidth - paddingRequiredBySection;
	}

	return requiredWidth;
}

const updateMoreDropDownData = (moreDropDown, controlsPopped) => {
	controlsPopped.forEach(control => {
		moreDropDown[control.dropDownSection].items.push({
			type: control.dropDownItemType,
			disabled: control.disabled,
			label: control.label,
			action: control.action,
			selected: control.selected
		});
	});
};

const updateToolbarData = (toolbarSections, moreDropDown, availableWidth) => {
	const controlsPopped = [];
	if (availableWidth) {
		let requiredWidth = calculateRequiredWidth(toolbarSections);
		if (requiredWidth > availableWidth) {
			const { rightAligned: rightAlignedSections, leftAligned: leftAlignedSections } = toolbarSections;
			let index = rightAlignedSections.length - 2;
			while (requiredWidth > availableWidth && index >= 0) {
				const currentSection = rightAlignedSections[index];
				requiredWidth = removeControlsInSection(currentSection, availableWidth, requiredWidth, controlsPopped);
				index--;
			}

			index = 0;
			while (requiredWidth > availableWidth && index < leftAlignedSections.length) {
				const currentSection = leftAlignedSections[index];
				requiredWidth = removeControlsInSection(currentSection, availableWidth, requiredWidth, controlsPopped);
				index++;
			}
		}
	}
	updateMoreDropDownData(moreDropDown, controlsPopped);
};

export default updateToolbarData;
