import {
	NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS,
	NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS
} from '../../../constants/cssClasses';

/**
 *
 * @param {{}} coeffects for the action
 * updating breakpoint based on user action after success http request
 */
function updateBreakPoint({
	action: {
		payload: {
			result: { Status, lineNumber, evaluationString }
		}
	},
	state: { editor, breakPoints },
	updateState
}) {
	const debugService = editor.debugPointService();
	if (Status === 'TRUE') {
		const tempBreakPoints = { ...breakPoints };
		if (breakPoints[lineNumber]) {
			if (evaluationString !== undefined) {
				updateDebugPoint(debugService, lineNumber, evaluationString, tempBreakPoints, updateState);
			} else {
				delete tempBreakPoints[lineNumber];
				updateState({ breakPoints: tempBreakPoints });
				debugService.removeDebugPointDecoration(lineNumber);
			}
		} else {
			updateDebugPoint(debugService, lineNumber, evaluationString, tempBreakPoints, updateState);
		}
		debugService.updateBreakPoints(tempBreakPoints);
	} else {
		debugService.removeDebugPointDecoration(lineNumber);
	}
	debugService.setFocus();
}

function updateDebugPoint(debugService, lineNumber, evaluationString, tempBreakPoints, updateState) {
	tempBreakPoints[lineNumber] = { fEvaluationString: evaluationString };
	updateState({ breakPoints: tempBreakPoints });
	if (evaluationString === '') {
		debugService.updateDebugPointDecoration(lineNumber, evaluationString, NOW_CODE_EDITOR_BREAKPOINT_MARKER_CLASS);
	} else {
		debugService.updateDebugPointDecoration(
			lineNumber,
			evaluationString,
			NOW_CODE_EDITOR_CONDITIONAL_BREAKPOINT_MARKER_CLASS
		);
	}
}

/**
 *
 * @param {{}} coeffects for the action
 * updating logpoint based on user action after success http request
 */
function updateLogPoint({
	action: {
		payload: {
			result: { Status, lineNumber, evaluationString }
		}
	},
	state: { editor, logPoints },
	updateState
}) {
	const debugService = editor.debugPointService();
	if (Status === 'TRUE') {
		const tempLogPoints = { ...logPoints };

		if (logPoints[lineNumber]) {
			if (evaluationString !== undefined && evaluationString !== '') {
				tempLogPoints[lineNumber] = { fEvaluationString: evaluationString };
				updateState({ logPoints: tempLogPoints });
				debugService.updateDebugPointDecoration(
					lineNumber,
					evaluationString,
					NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS
				);
			} else {
				delete tempLogPoints[lineNumber];
				updateState({ logPoints: tempLogPoints });
				debugService.removeDebugPointDecoration(lineNumber);
			}
		} else {
			tempLogPoints[lineNumber] = { fEvaluationString: evaluationString };
			updateState({ logPoints: tempLogPoints });
			debugService.updateDebugPointDecoration(
				lineNumber,
				evaluationString,
				NOW_CODE_EDITOR_LOGPOINT_MARKER_CLASS
			);
		}
		debugService.updateLogPoints(tempLogPoints);
	} else {
		debugService.removeDebugPointDecoration(lineNumber);
	}
	debugService.setFocus();
}

export { updateBreakPoint, updateLogPoint };
