import { actionTypes } from '@servicenow/ui-core';

const actionHandlers = {
	[actionTypes.COMPONENT_RENDERED]: {
		effect({ host, properties: { isOpenKeymap } }) {
			if (isOpenKeymap) {
				// setting the default focus to first shortcut key
				const firstShortcutEle = host.shadowRoot.querySelector('.first-shortcut-action');
				if (firstShortcutEle) {
					setTimeout(() => {
						firstShortcutEle.focus();
					}, 50);
				}
			}
		},
		stopPropagation: true
	}
};

export default actionHandlers;
