import '@servicenow/now-modal';
import '@servicenow/now-dropdown';
import '@servicenow/now-button';
import '../../sn-code-editor-toolbar';
import '../../sn-code-editor-keyboard-shortcut';
import getToolbarData from './helpers/getToolbarData';

const setButtonElement = (updateState, elm) => {
	updateState({ contextMenuButtonElement: elm });
};

const view = (state, { updateState }) => {
	const {
		isOpenKeymap,
		openedContextMenu,
		contextMenuItems,
		contextMenuButtonElement,
		isContextMenuPositionedAbove,
		properties: { hideToolbar, customCSS }
	} = state;
	const positionContextMenu = isContextMenuPositionedAbove ? ['top-start bottom-start'] : ['top-start top-start'];
	const { toolBarSections, moreDropDown, keyBoardShortcuts } = getToolbarData(state);

	return (
		<div className="now-code-editor">
			{customCSS ? <style type="text/css">{customCSS}</style> : ''}
			<div className={`now-code-editor-container ${hideToolbar ? 'without-toolbar' : 'with-toolbar'}`} />
			{hideToolbar === false ? (
				<sn-code-editor-toolbar
					toolBarSections={toolBarSections}
					moreDropDown={moreDropDown}
				></sn-code-editor-toolbar>
			) : (
				''
			)}
			<sn-code-editor-keyboard-shortcut
				isOpenKeymap={isOpenKeymap}
				keyBoardShortcuts={keyBoardShortcuts}
			></sn-code-editor-keyboard-shortcut>
			<now-button
				className="now-code-editor-context-menu-btn"
				hook-insert={({ elm }) => setButtonElement(updateState, elm)}
				size="sm"
			/>
			<now-dropdown-panel
				items={contextMenuItems}
				position={positionContextMenu}
				select="none"
				opened={openedContextMenu.isOpen}
				target-ref={contextMenuButtonElement}
				id="now-dropdown-panel"
				hideLabel={true}
				size="sm"
			/>
		</div>
	);
};

export default view;
