import monaco from '../NowMonaco';

function addGlyphHoverWidget(editor) {
	const glyphHoverOverlayWidget = {
		domNode: null,
		getId: function() {
			return 'now-code-editor-glyphHoverWidget';
		},
		getDomNode: function() {
			let domNode = null;
			if (!this.domNode) {
				domNode = document.createElement('div');
				domNode.className = 'now-code-editor-monaco-hover hidden';
				domNode.setAttribute('aria-hidden', 'true');
				domNode.setAttribute('role', 'tooltip');
				domNode.style.cursor = 'pointer';
				domNode.oncontextmenu = function(e) {
					e.preventDefault();
				};
			}
			this.domNode = this.domNode || domNode;
			return this.domNode;
		},
		getPosition: function() {
			return null;
		}
	};
	editor.addOverlayWidget(glyphHoverOverlayWidget);
	return glyphHoverOverlayWidget;
}

function showGlyphHoverWidget(editor, lineNumber, glyphHoverOverlayWidget) {
	const glyphHoverOverlayWidgetNode = glyphHoverOverlayWidget.domNode;
	glyphHoverOverlayWidgetNode.classList.remove('hidden');

	const editorLayout = editor.getLayoutInfo();
	const topForLineNumber = editor.getTopForLineNumber(lineNumber);
	const editorScrollTop = editor.getScrollTop();
	const lineHeight = editor.getOption(monaco.editor.EditorOption.lineHeight);
	const nodeHeight = glyphHoverOverlayWidgetNode.clientHeight;
	const top = topForLineNumber - editorScrollTop - (nodeHeight - lineHeight) / 2;

	glyphHoverOverlayWidgetNode.style.left = `${editorLayout.glyphMarginLeft}px`;
	glyphHoverOverlayWidgetNode.style.top = `${Math.max(Math.round(top + 4), 4)}px`;
}

function hideGlyphHoverWidget(glyphHoverOverlayWidget) {
	glyphHoverOverlayWidget.domNode.classList.add('hidden');
}

// we can destory this overlay widget by calling this method
function destroyGlyphHoverWidget(editor, glyphHoverOverlayWidget) {
	editor.removeOverlayWidget(glyphHoverOverlayWidget);
}

export { addGlyphHoverWidget, showGlyphHoverWidget, hideGlyphHoverWidget, destroyGlyphHoverWidget };
